import React, { Component } from 'react'
import MaterialTable from 'material-table'
import Papa from 'papaparse'

const columns = [
    {
        title: 'Report Date',
        field: 'Report Date',
    },

    {
        title: 'State',
        field: 'State',
    },

    {
        title: 'Cummulative Cases',
        field: 'Cummulative Cases',
        filtering: false,
    },
    {
        title: 'New Cases',
        field: 'New Cases',
        filtering: false,
    },
    {
        title: 'Average New Cases',
        field: 'Average New Cases',
        filtering: false,
    },
]

class CasesStateDataTable extends Component {

    constructor(props) {
        super(props)
        this.state = {
            data: []
        }
        this.updateData = this.updateData.bind(this)
    }

    updateData(result) {
        let y = [];
        const results = result.data
        for (let i = 0; i < results.length - 1; i++) {
            y.push({
                "Report Date": results[i]['report_date'],
                "State": results[i]['state'],
                "Cummulative Cases": results[i]['cases'],
                "New Cases": results[i]['new_cases'],
                "Average New Cases": results[i]['case_avg_rate'].toFixed(3),
            })
        }
        y.reverse()
        this.setState({ data: y })
    }

    componentDidMount() {
        Papa.parse('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/master/data/state/state_full.csv', {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    componentWillUnmount() {
        this.setState({ data: [] })
    }

    render() {
        return (
            <div style={{ width: '100%' }}>
                <MaterialTable
                    title="All State Cases"
                    columns={columns}
                    data={this.state.data}
                    options={{
                        search: true,
                        paging: true,
                        header: true,
                        filtering: true,
                        /*headerStyle: {
                            backgroundColor: '#01579b',
                            color: '#FFF'
                        },*/
                    }}
                />
            </div>
        )
    }
}

export default CasesStateDataTable