import { HashLink as Link } from 'react-router-hash-link';
import React from 'react';
import './css/About.css'

function About() {
    return (
        <div className='about'>
            <nav className="aboutNav">
                <ul className="aboutNav-menu">
                    <li className="aboutNav-item menuHeader">
                        Navigator
                    </li>
                    <li className="aboutNav-item">
                        <Link className="aboutNav-link" to="/about/#intro">Motivation</Link>
                    </li>
                    <li className="aboutNav-item">
                        <Link className="aboutNav-link" to="/about/#uniqueness">What Makes This Site Unique?</Link>
                    </li>
                    <li className="aboutNav-item">
                        <Link className="aboutNav-link" to="/about/#abtChris">About Chris</Link>
                    </li>
                    <li className="aboutNav-item">
                        <Link className="aboutNav-link" to="/about/#aboutProject">About the SLU OpenGIS Project</Link>
                    </li>
                    <li className="aboutNav-item" id="bottomItem">
                        <Link className="aboutNav-link" to="/about/#aboutSLU">About SLU</Link>
                    </li>
                </ul>
            </nav>
            <h1 className="sectionTitle" id="intro">About This Website</h1>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="intro">Motivation</h2>
                    <div className="textBox">
                        <p>
                            Chris Prener has been maintaining this site has been updated since March as a means for communicating information about the SARS-CoV-2 pandemic in Missouri. When the site was initially launched, the State of Missouri was not providing much in the way of data about the pandemic on the Department of Health and Senior Services website. Though that has changed to some degree, it also does not provide a comprehensive resource for data from Missouri. Moreover, their dashboard has a number of quirks, occasional problems, and does not always follow best practices for mapping or visualizing data.
                        </p>
                    </div>
                </div>
            </section>

            <section className="segments">
                <div className="container" id="uniqueness">
                    <h2 className="sectionTitle">What Makes This Site Unique</h2>
                    <div className="textBox">
                        <p>
                            Unlike other interactive maps being used to track the outbreak, the interactive maps include the counties in Illinois, Kansas, and Oklahoma that are part of Missouris metropolitan areas. This is also true for the tables and static plots of data from metropolitan areas. The goal is to provide residents of Missouris largest cities with a more comprehensive snapshot of the state of the outbreak in the area around them, even if it is across a state line.
                        </p>
                        <p>
                            The site also integrates data from a number of other data sources that the State of Missouri does not provide on their dashboard. These include data about health disparities and hospitalizations in the St.Louis area, testing and infection data from surrounding states, and zip-code data for St.Louis as well.
                        </p>
                    </div>
                </div>
            </section>

            <section className="segments">
                <div className="container" id="abtChris">
                    <h2 className="sectionTitle">About Chris</h2>
                    <div className="textBox">
                        <p>
                            Chris is an urban and medical sociologist with an interest in mixed methods research designs that incorporate spatial data. His current book project examines the effect of neighborhood context and conditions on emergency medical services work, particularly with patients who have mental illnesses or substance use disorders. Chris also as a grant to conduct pilot work on the effects of historic redlining on contemporary health disparities in St.Louis. He is also part of a research team examining the effects of literacy on mental health service use and recovery, and he is engaged in a number of St.Louis specific projects on street closures, crime, arson, and vacancy. Chris is an Assistant Professor in the Department of Sociology and Anthropology at <a className="aboutLink" href="http://wwww.slu.edu" >Saint Louis University</a>. More details are available at his <a className="aboutLink" href="https://chris-prener.github.io">website</a> and he can be contacted at <a className="aboutLink" href="mailto:chris.prener@slu.edu">chris.prener@slu.edu</a>.
                        </p>
                    </div>
                </div>
            </section>

            <section className="segments">
                <div className="container" id="aboutProject">
                    <h2 className="sectionTitle">About the SLU OpenGIS Project</h2>
                    <div className="textBox">
                        <p>
                            The <a className="aboutLink" href="https://slu-dss.github.io">SLU Data Science Seminar</a> is the host of Chriss <a className="aboutLink" href="https://slu-opengis.github.io">openGIS</a> project. The project is a collaborative effort with <a className="aboutLink" href="http://wwww.slu.edu">Saint Louis University</a> students to curate and host GIS data about the St.Louis area in an accessible manner. We also maintain several R packages that contain tools for working with spatial data. More details about our projects and our commitment to creating an inclusive, welcoming data science community are available on our <a className="aboutLink" href="https://slu-opengis.github.io/about/" >website</a>.
                        </p>
                    </div>
                </div>
            </section>

            <section className="segments">
                <div className="container" id="aboutSLU">
                    <h2 className="sectionTitle">About SLU</h2>
                    <div className="textBox">
                        <p>
                            Founded in 1818, <a className="aboutLink" href="http://wwww.slu.edu" >Saint Louis University</a> is one of the nations oldest and most prestigious Catholic institutions. Rooted in Jesuit values and its pioneering history as the first university west of the Mississippi River, SLU offers nearly 13,000 students a rigorous, transformative education of the whole person. At the core of the Universitys diverse community of scholars is SLUs service-focused mission, which challenges and prepares students to make the world a better, more just place.
                        </p>
                    </div>
                </div>
            </section>


        </div>
    );
}

export default About;
