import React, { Component } from 'react'
import Papa from 'papaparse'
import { Chart } from 'chart.js';

let metroCaseChart;

let dates = [];
let clintonCases = [];
let monroeCases = [];
let jerseyCases = [];
let bondCases = [];
let stcharlesCases = [];
let stclairCases = [];
let madisonCases = [];
let lincolnCases = [];
let jeffersonCases = [];
let franklinCases = [];
let stlouisCases = [];
let stlouiscityCases = [];

let clintonNew = [];
let monroeNew = [];
let jerseyNew = [];
let bondNew = [];
let stcharlesNew = [];
let stclairNew = [];
let madisonNew = [];
let lincolnNew = [];
let jeffersonNew = [];
let franklinNew = [];
let stlouisNew = [];
let stlouiscityNew = [];

let clintonCapita = [];
let monroeCapita = [];
let jerseyCapita = [];
let bondCapita = [];
let stcharlesCapita = [];
let stclairCapita = [];
let madisonCapita = [];
let lincolnCapita = [];
let jeffersonCapita = [];
let franklinCapita = [];
let stlouisCapita = [];
let stlouiscityCapita = [];

function cummCases(results) {
    for (let i = 0; i < results.length; i++) {
        if (results[i]['county'] == 'Clinton' && results[i]['state'] == 'Illinois') {
            clintonCases.push(results[i]['cases'])
            clintonNew.push(results[i]['case_avg'])
            clintonCapita.push(results[i]['case_rate'])
            dates.push(results[i]['report_date'])
        }
        if (results[i]['county'] == 'Monroe' && results[i]['state'] == 'Illinois') {
            monroeCases.push(results[i]['cases'])
            monroeNew.push(results[i]['case_avg'])
            monroeCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'Jersey' && results[i]['state'] == 'Illinois') {
            jerseyCases.push(results[i]['cases'])
            jerseyNew.push(results[i]['case_avg'])
            jerseyCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'Bond' && results[i]['state'] == 'Illinois') {
            bondCases.push(results[i]['cases'])
            bondNew.push(results[i]['case_avg'])
            bondCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'St. Charles' && results[i]['state'] == 'Missouri') {
            stcharlesCases.push(results[i]['cases'])
            stcharlesNew.push(results[i]['case_avg'])
            stcharlesCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'St. Clair' && results[i]['state'] == 'Illinois') {
            stclairCases.push(results[i]['cases'])
            stclairNew.push(results[i]['case_avg'])
            stclairCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'Madison' && results[i]['state'] == 'Illinois') {
            madisonCases.push(results[i]['cases'])
            madisonNew.push(results[i]['case_avg'])
            madisonCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'Lincoln' && results[i]['state'] == 'Missouri') {
            lincolnCases.push(results[i]['cases'])
            lincolnNew.push(results[i]['case_avg'])
            lincolnCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'Jefferson' && results[i]['state'] == 'Missouri') {
            jeffersonCases.push(results[i]['cases'])
            jeffersonNew.push(results[i]['case_avg'])
            jeffersonCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'Franklin' && results[i]['state'] == 'Missouri') {
            franklinCases.push(results[i]['cases'])
            franklinNew.push(results[i]['case_avg'])
            franklinCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'St. Louis' && results[i]['state'] == 'Missouri') {
            stlouisCases.push(results[i]['cases'])
            stlouisNew.push(results[i]['case_avg'])
            stlouisCapita.push(results[i]['case_rate'])
        }
        if (results[i]['county'] == 'St. Louis City' && results[i]['state'] == 'Missouri') {
            stlouiscityCases.push(results[i]['cases'])
            stlouiscityNew.push(results[i]['case_avg'])
            stlouiscityCapita.push(results[i]['case_rate'])
        }
    }
}

function switchCumm() {
    metroCaseChart.destroy()
    options['title']['text'] = 'Metro Cummulative Cases'
    data['datasets'][0]['data'] = clintonCases
    data['datasets'][1]['data'] = monroeCases
    data['datasets'][2]['data'] = jerseyCases
    data['datasets'][3]['data'] = bondCases
    data['datasets'][4]['data'] = stcharlesCases
    data['datasets'][5]['data'] = stclairCases
    data['datasets'][6]['data'] = madisonCases
    data['datasets'][7]['data'] = lincolnCases
    data['datasets'][8]['data'] = jeffersonCases
    data['datasets'][9]['data'] = franklinCases
    data['datasets'][10]['data'] = stlouisCases
    data['datasets'][11]['data'] = stlouiscityCases
    let newChart = new Chart('metroCaseChart', {
        type: 'line',
        data: data,
        options: options
    })
    metroCaseChart = newChart
}

function switchNew() {
    metroCaseChart.destroy()
    options['title']['text'] = 'Metro New Cases'
    data['datasets'][0]['data'] = clintonNew
    data['datasets'][1]['data'] = monroeNew
    data['datasets'][2]['data'] = jerseyNew
    data['datasets'][3]['data'] = bondNew
    data['datasets'][4]['data'] = stcharlesNew
    data['datasets'][5]['data'] = stclairNew
    data['datasets'][6]['data'] = madisonNew
    data['datasets'][7]['data'] = lincolnNew
    data['datasets'][8]['data'] = jeffersonNew
    data['datasets'][9]['data'] = franklinNew
    data['datasets'][10]['data'] = stlouisNew
    data['datasets'][11]['data'] = stlouiscityNew
    let newChart = new Chart('metroCaseChart', {
        type: 'line',
        data: data,
        options: options
    })
    metroCaseChart = newChart
}

function switchCapita() {
    metroCaseChart.destroy()
    options['title']['text'] = 'Metro Cases Per Capita'
    data['datasets'][0]['data'] = clintonCapita
    data['datasets'][1]['data'] = monroeCapita
    data['datasets'][2]['data'] = jerseyCapita
    data['datasets'][3]['data'] = bondCapita
    data['datasets'][4]['data'] = stcharlesCapita
    data['datasets'][5]['data'] = stclairCapita
    data['datasets'][6]['data'] = madisonCapita
    data['datasets'][7]['data'] = lincolnCapita
    data['datasets'][8]['data'] = jeffersonCapita
    data['datasets'][9]['data'] = franklinCapita
    data['datasets'][10]['data'] = stlouisCapita
    data['datasets'][11]['data'] = stlouiscityCapita
    let newChart = new Chart('metroCaseChart', {
        type: 'line',
        data: data,
        options: options
    })
    metroCaseChart = newChart
}

function parseData(url) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            cummCases(results.data)
            metroCaseChart.update()
        }
    });
}

let data = {
    labels: dates,
    datasets: [
        {
            label: 'Clinton',
            data: clintonCases,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
            ],

            borderWidth: 1,
        },

        {
            label: "Monroe",
            data: monroeCases,

            backgroundColor: [
                'rgba(54, 162, 235, 0.2)'
            ],

            borderColor: [
                'rgba(54, 162, 235, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "Jersey",
            data: jerseyCases,

            backgroundColor: [
                'rgba(255, 206, 86, 0.2)'
            ],

            borderColor: [
                'rgba(255, 206, 86, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "Bond",
            data: bondCases,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "St. Charles",
            data: stcharlesCases,

            backgroundColor: [
                'rgba(153, 102, 255, 0.2)'
            ],

            borderColor: [
                'rgba(153, 102, 255, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "St. Clair",
            data: stclairCases,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },
        {
            label: "Madison",
            data: madisonCases,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },
        {
            label: "Lincoln",
            data: lincolnCases,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },
        {
            label: "Jefferson",
            data: jeffersonCases,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },
        {
            label: "Franklin",
            data: franklinCases,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },
        {
            label: "St. Louis",
            data: stlouisCases,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },
        {
            label: "St. Louis City",
            data: stlouiscityCases,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },
    ],
}

let options = {
    elements: {
        point: {
            radius: 1
        }
    },
    legend: {
        labels: {
            fontSize: 15,
            /*fontColor: "rgba(255,255,255,0.65)",*/
        }
    },
    responsiveAnimationDuration: 1050,
    title: {
        display: true,
        text: 'Metro Cummulative Cases',
        fontSize: 30
    },
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    beginAtZero: true,
                    fontSize: 18,
                },
            },
        ],
        xAxes: [{
            ticks: {
                fontSize: 18,
            },
            type: 'time',
            time: {
                unit: 'month',
                unitStepSize: 1,
                displayFormats: {
                    'month': 'MMM YYYY',
                }
            }
        }],
    },
}

class MetroCasesChart extends Component {

    componentDidMount() {
        metroCaseChart = new Chart("metroCaseChart", {
            type: 'line',
            data: data,
            options: options,
        })
        parseData('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/5d34a7af3c6df9db4ab914eb7d710be9689e1f1d/data/county/county_full.csv')
    }

    componentWillUnmount() {
        metroCaseChart.destroy()
        dates = [];
        clintonCases = [];
        monroeCases = [];
        jerseyCases = [];
        bondCases = [];
        stcharlesCases = [];
        stclairCases = [];
        madisonCases = [];
        lincolnCases = [];
        jeffersonCases = [];
        franklinCases = [];
        stlouisCases = [];
        stlouiscityCases = [];

        clintonNew = [];
        monroeNew = [];
        jerseyNew = [];
        bondNew = [];
        stcharlesNew = [];
        stclairNew = [];
        madisonNew = [];
        lincolnNew = [];
        jeffersonNew = [];
        franklinNew = [];
        stlouisNew = [];
        stlouiscityNew = [];
    }

    render() {
        return (
            <div style={{ position: "relative", height: 650 }}>
                <div className="buttonContainer">
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchCumm()}>Cummulative</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchNew()}>Average New Cases</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchCapita()}>Per Captita Rate</div>
                    </button>
                </div>
                <div className="chart">
                    <canvas id="metroCaseChart" style={{ width: 600, height: 500, backgroundColor: "white", marginTop: 30, borderRadius: 8 }} ></canvas>
                </div>
            </div >
        )
    }

}

export default MetroCasesChart