import React, { Component } from 'react'
import MaterialTable from 'material-table'
import Papa from 'papaparse'

const columns = [
    {
        title: 'Report Date',
        field: 'Report Date',
    },

    {
        title: 'State',
        field: 'State',
    },
    {
        title: 'County',
        field: 'County',
    },

    {
        title: 'Cummulative Cases',
        field: 'Cummulative Cases',
        filtering: false,
    },
    {
        title: 'New Cases',
        field: 'New Cases',
        filtering: false,
    },
    {
        title: 'Average New Cases',
        field: 'Average New Cases',
        filtering: false,
    },
    {
        title: 'Per Capita Rate',
        field: 'Per Capita Rate',
        filtering: false,
    }
]

let formatTwoDigits = (digit) => ("0" + digit).slice(-2);
let today = new Date()
let twodate = today.getFullYear() + '-' + (formatTwoDigits(today.getMonth() + 1)) + '-' + (formatTwoDigits(today.getDate() - 2))
let onedate = today.getFullYear() + '-' + (formatTwoDigits(today.getMonth() + 1)) + '-' + (formatTwoDigits(today.getDate() - 1))
let zerodate = today.getFullYear() + '-' + (formatTwoDigits(today.getMonth() + 1)) + '-' + (formatTwoDigits(today.getDate()))

class CasesCountyDataTable extends Component {

    constructor(props) {
        super(props)
        this.state = {
            data: []
        }
        this.updateData = this.updateData.bind(this)
    }

    updateData(result) {
        let y = [];
        const results = result.data
        for (let i = 0; i < results.length - 1; i++) {
            if (results[i]['report_date'] == zerodate || results[i]['report_date'] == onedate || results[i]['report_date'] == twodate) {
                y.push({
                    "Report Date": results[i]['report_date'],
                    "State": results[i]['state'],
                    "County": results[i]['county'],
                    "Cummulative Cases": results[i]['cases'],
                    "New Cases": results[i]['new_cases'],
                    "Average New Cases": results[i]['case_avg'].toFixed(3),
                    "Per Capita Rate": results[i]['case_rate'].toFixed(3)
                })
            }
        }
        y.reverse()
        this.setState({ data: y })
    }

    componentDidMount() {
        Papa.parse('https://git.cs.slu.edu/api/v4/projects/2267/repository/files/site%2Fdata%2Fcounty_full_recent.csv/raw?ref=master&private_token=8yfE4j_YrY2kvypQ8xHc&fbclid=IwAR08kHnG_98YM_Zv-HJtMMexBaz8uZJqcPKjwpYm2DoHdMqHpvGwiDlpPmQ', {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    componentWillUnmount() {
        this.setState({ data: [] })
    }

    render() {
        return (
            <div style={{ width: '100%' }}>
                <MaterialTable
                    title="County Cases"
                    columns={columns}
                    data={this.state.data}
                    options={{
                        search: true,
                        paging: true,
                        header: true,
                        filtering: true,
                        /*headerStyle: {
                            backgroundColor: '#01579b',
                            color: '#FFF'
                        },*/
                    }}
                />
            </div>
        )
    }
}

export default CasesCountyDataTable