import React, { Component } from 'react'
import { Chart } from 'react-chartjs-2'
import Papa from 'papaparse'

let regionChart;

let regionLabels = ['Northeast', 'Midwest', 'South', 'West'];
let northeastCases = [0];
let midwestCases = [0];
let southCases = [0];
let westCases = [0];

function doStuff(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['state'] === 'Connecticut' || data[i]['state'] === 'Maine' || data[i]['state'] === 'Massechusetts' || data[i]['state'] === 'New Hampshire' || data[i]['state'] === 'Rhode Island' || data[i]['state'] === 'Vermont' || data[i]['state'] === 'New Jersey' || data[i]['state'] === 'New York' || data[i]['state'] === 'Pennsylvania') {
            northeastCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Illinois' || data[i]['state'] === 'Indiana' || data[i]['state'] === 'Michigan' || data[i]['state'] === 'Ohio' || data[i]['state'] === 'Wisconsin' || data[i]['state'] === 'Iowa' || data[i]['state'] === 'Kansas' || data[i]['state'] === 'Minnesota' || data[i]['state'] === 'Missouri' || data[i]['state'] === 'Nebraska' || data[i]['state'] === 'North Dakota' || data[i]['state'] === 'South Dakota') {
            midwestCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Delaware' || data[i]['state'] === 'Florida' || data[i]['state'] === 'Georgia' || data[i]['state'] === 'Maryland' || data[i]['state'] === 'North Carolina' || data[i]['state'] === 'South Carolina' || data[i]['state'] === 'Virginia' || data[i]['state'] === 'District of Columbia' || data[i]['state'] === 'West Virginia' || data[i]['state'] === 'Alabama' || data[i]['state'] === 'Kentucky' || data[i]['state'] === 'Mississippi' || data[i]['state'] === 'Tennessee' || data[i]['state'] === 'Arkansas' || data[i]['state'] === 'Louisiana' || data[i]['state'] === 'Oklahoma' || data[i]['state'] === 'Texas') {
            southCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Arizona' || data[i]['state'] === 'Colorado' || data[i]['state'] === 'Idaho' || data[i]['state'] === 'Montana' || data[i]['state'] === 'Nevada' || data[i]['state'] === 'New Mexico' || data[i]['state'] === 'Utah' || data[i]['state'] === 'Wyoming' || data[i]['state'] === 'Alaska' || data[i]['state'] === 'California' || data[i]['state'] === 'Hawaii' || data[i]['state'] === 'Oregon' || data[i]['state'] === 'Washington') {
            westCases[0] += data[i]['cases']
        }
    }
}

function parseData(url, callBack) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            callBack(results.data);
            regionChart.update()
        }
    });
}

let data = {
    labels: regionLabels,
    datasets: [
        {
            label: 'Cases in Regions (Schools)',
            data: [northeastCases, midwestCases, southCases, westCases],

            //backgroundColor: 'rgba(54, 162, 235, 0.2)',
            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,
        },
    ],
}

let options = {
    title: {
        display: true,
        text: 'Cases Based by Regions (Schools)',
        fontSize: 30
    },
    legend: {
        labels: {
            fontSize: 15,
            /*fontColor: "rgba(255,255,255,0.65)",*/
        }
    },
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    fontSize: 18,
                    beginAtZero: true,
                    display: false,
                },
            },
        ],
        xAxes: [
            {
                ticks: {
                    fontSize: 14,
                    min: 0,
                    display: false,
                },
            },
        ],
    },
}

class RegionUniversitiesChart extends Component {

    componentDidMount() {
        regionChart = new Chart("regionChart", {
            type: 'doughnut',
            data: data,
            options: options
        })
        parseData('https://raw.githubusercontent.com/nytimes/covid-19-data/master/colleges/colleges.csv', doStuff);
    }

    componentWillUnmount() {
        northeastCases = [0];
        midwestCases = [0];
        southCases = [0];
        westCases = [0];
    }

    render() {

        return (
            <div style={{ position: "relative", height: 500 }}>
                <canvas id="regionChart" style={{ width: 600, height: 400, backgroundColor: "white", borderRadius: 8 }}></canvas>
            </div >

        )
    }
}

export default RegionUniversitiesChart