import React, { Component } from 'react'
import { Chart } from 'react-chartjs-2'

let tutorialChart;

let labels = ['Yes', 'No']
let responses = [80, 20]

var data = {
    labels: labels,
    datasets: [
        {
            label: 'Responses',
            data: responses,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
            ],

            borderWidth: 1,
        }
    ],
}
var lineData = {
    labels: [2005, 2010, 2015, 2020],
    datasets: [
        {
            label: 'Total Orders',
            data: [300, 30000, 30000, 300000],

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
            ],

            borderWidth: 1,
        },
    ],
}

var options = {
    responsiveAnimationDuration: 1050,
    title: {
        display: true,
        text: 'Pineapple on Pizza?',
        fontSize: 30,
    },

    legend: {
        labels: {
            fontSize: 15,
        }
    },
    responsive: true,
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    fontSize: 18,
                    beginAtZero: true,
                },
            },
        ],

        xAxes: [
            {
                ticks: {
                    fontSize: 18,
                }
            },
        ]
    },
}

function switchBar() {
    tutorialChart.destroy()
    options['title']['text'] = "Pineapple on Pizza?"
    let newChart = new Chart("tutorialChart", {
        type: 'bar',
        data: data,
        options: options,
    })
    tutorialChart = newChart
}

function switchPie() {
    tutorialChart.destroy()
    options['title']['text'] = "Pineapple on Pizza?"
    let newChart = new Chart("tutorialChart", {
        type: 'pie',
        data: data,
        options: options,
    })
    tutorialChart = newChart
}

function switchDoughnut() {
    tutorialChart.destroy()
    options['title']['text'] = "Pineapple on Pizza?"
    let newChart = new Chart("tutorialChart", {
        type: 'doughnut',
        data: data,
        options: options,
    })
    tutorialChart = newChart
}

function switchLine() {
    tutorialChart.destroy()
    options['title']['text'] = "Pineapple on Pizza Popularity Over Time"
    let newChart = new Chart("tutorialChart", {
        type: 'line',
        data: lineData,
        options: options,
    })
    tutorialChart = newChart
}

class TutorialChart extends Component {

    componentDidMount() {
        tutorialChart = new Chart("tutorialChart", {
            type: 'bar',
            data: data,
            options: options
        })
    }

    render() {
        return (
            <div>
                <div className="buttonContainer">
                    <button className="pushable" onClick={() => switchBar()}>
                        <div className="shadow"></div>
                        <div className="front">Bar</div>
                    </button>
                    <button className="pushable" onClick={() => switchPie()}>
                        <div className="shadow"></div>
                        <div className="front">Pie</div>
                    </button>
                    <button className="pushable" onClick={() => switchDoughnut()}>
                        <div className="shadow"></div>
                        <div className="front">Doughnut</div>
                    </button>
                    <button className="pushable" onClick={() => switchLine()}>
                        <div className="shadow"></div>
                        <div className="front">Line</div>
                    </button>
                </div>
                <div className="chart">
                    <canvas id="tutorialChart" style={{ width: 600, height: 500, backgroundColor: "white", marginTop: 30, borderRadius: 8 }}></canvas>
                </div>
            </div>
        )
    }
}

export default TutorialChart