import React, { Component } from 'react'
import { Chart } from 'react-chartjs-2'
import Papa from 'papaparse'

let moUniversitiesChart;

let moLabels = [];
let moCases = [];

function doStuff(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['state'] === 'Missouri') {
            moLabels.push(data[i]['college'])
            moCases.push(data[i]['cases'])
        }
    }
}

function parseData(url, callBack) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            callBack(results.data);
            moUniversitiesChart.update()
        }
    });
}

let data = {
    labels: moLabels,
    datasets: [
        {
            label: 'Cases in Missouri Schools',
            data: moCases,

            backgroundColor: 'rgba(54, 162, 235, 0.2)',

            borderColor: 'rgba(54, 162, 235, 1)',

            borderWidth: 1,
        },
    ],
}

let options = {
    title: {
        display: true,
        text: 'Cases in Missouri Schools',
        fontSize: 30
    },
    legend: {
        labels: {
            fontSize: 15,
            /*fontColor: "rgba(255,255,255,0.65)",*/
        }
    },
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    fontSize: 18,
                    beginAtZero: true,
                },
            },
        ],
        xAxes: [
            {
                ticks: {
                    fontSize: 14,
                    min: 0,
                    max: 3500,
                    stepSize: 100,
                },
            },
        ],
    },
}

class MOUniversitiesChart extends Component {

    componentDidMount() {
        moUniversitiesChart = new Chart("moUniversitiesChart", {
            type: 'bar',
            data: data,
            options: options
        })
        parseData('https://raw.githubusercontent.com/nytimes/covid-19-data/master/colleges/colleges.csv', doStuff);
    }

    componentWillUnmount() {
        moUniversitiesChart.destroy()
        moLabels = [];
        moCases = [];
    }

    render() {
        return (
            <div style={{ position: "relative", height: 500 }}>
                <canvas id="moUniversitiesChart" style={{ width: 600, height: 500, backgroundColor: "white", borderRadius: 8 }}></canvas>
            </div >

        )
    }
}

export default MOUniversitiesChart