import React, { Component } from 'react'
import Papa from 'papaparse'

function formatNumber(num) {
    return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
}

class DataHubTable extends Component {

    constructor(props) {
        super(props)
        this.state = {
            cases: [],
            deaths: []
        }
        this.updateData = this.updateData.bind(this)
    }

    componentDidMount() {
        Papa.parse('https://git.cs.slu.edu/api/v4/projects/2267/repository/files/site%2Fdata%2Fdatahub.csv/raw?ref=master&private_token=8yfE4j_YrY2kvypQ8xHc&fbclid=IwAR3cmTJXfeYQj2X9LLbR81yCZeeKbQJyHcfkS_Ey3XLqSk1RVmjs5NpWzrQ', {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    componentWillUnmount() {
        this.setState({ cases: [], deaths: [] })
    }

    updateData(results) {
        let x = [];
        let y = [];
        let result = results.data
        for (let i = 0; i < result.length - 1; i++) {
            console.log('hi')
            x.push(formatNumber(result[i]['cases']))
            y.push(formatNumber(result[i]['deaths']))
        }
        this.setState({ cases: x, deaths: y })
    }

    render() {
        return (
            <div className="table">
                <table>
                    <thead>
                        <tr>
                            <th></th>
                            <th>Cases</th>
                            <th>Deaths</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Global</td>
                            <td>{this.state.cases[0]}</td>
                            <td>{this.state.deaths[0]}</td>
                        </tr>
                        <tr>
                            <td>US</td>
                            <td>{this.state.cases[1]}</td>
                            <td>{this.state.deaths[1]}</td>
                        </tr>
                        <tr>
                            <td>Missouri</td>
                            <td>{this.state.cases[2]}</td>
                            <td>{this.state.deaths[2]}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        )
    }
}

export default DataHubTable