import React, { Component } from 'react'
import Papa from 'papaparse'
import MaterialTable from 'material-table'
import '../pages/css/Data.css'

const columns = [
    {
        title: 'County',
        field: 'County',
    },
    {
        title: 'Cummulative Complete Vaccinations',
        field: 'Cummulative Complete Vaccinations',
        filtering: false,
    },
    {
        title: 'Rate per 1,000 Residents',
        field: 'Rate per 1,000 Residents',
        filtering: false,
    },
]

let formatTwoDigits = (digit) => ("0" + digit).slice(-2);
let today = new Date()
let date = today.getFullYear() + '-' + (formatTwoDigits(today.getMonth() + 1)) + '-' + (formatTwoDigits(today.getDate() - 3))

class VaccinationsCompletedMap extends Component {

    constructor(props) {
        super(props);
        this.state = {
            content: <iframe className="mapBox" src="https://cs.slu.edu/student/ando/capstone/complete_vax.html?fbclid=IwAR0PZvdfe5G-0Odr3b0F4B0S8SGDHSq0KARXYcSXRKJuAac7pmEnrwHze7s" className="home_map" title="map1"></iframe>,
            data: []
        }
        this.updateData = this.updateData.bind(this)
    }

    updateData(result) {
        let y = [];
        const results = result.data
        for (let i = 0; i < results.length - 1; i++) {
            if (results[i]['report_date'] == date) {
                y.push({
                    "County": results[i]['county'],
                    "Cummulative Complete Vaccinations": results[i]['complete'],
                    "Rate per 1,000 Residents": results[i]['complete_rate'].toFixed(3)
                })
            }
        }
        this.setState({ data: y })
    }

    componentDidMount() {
        Papa.parse(('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/master/data/source/mo_daily_vaccines/mo_daily_vaccines_' + date + '.csv'), {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    componentWillUnmount() {
        this.setState({ data: [] })
    }

    updateStatic() {
        this.setState({ content: <img src={'https://slu-opengis.github.io/covid_daily_viz/img/county/t_vaccine_complete.png'}></img> })
    }

    updateInteractive() {
        this.setState({ content: <iframe className="mapBox" src="https://cs.slu.edu/student/ando/capstone/complete_vax.html?fbclid=IwAR0PZvdfe5G-0Odr3b0F4B0S8SGDHSq0KARXYcSXRKJuAac7pmEnrwHze7s" className="home_map" title="map1"></iframe> })
    }

    updateDataTable() {
        this.setState({
            content:
                <div style={{ marginTop: 50 }}>
                    <MaterialTable
                        title="Vaccinations Complete Map"
                        columns={columns}
                        data={this.state.data}
                        options={{
                            search: true,
                            paging: true,
                            header: true,
                            filtering: true,
                        }}
                    />
                </div>
        })
    }

    render() {
        return (
            <div className='data' style={{ background: 'transparent', width: '100%' }}>
                <div className='buttonContainer'>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateInteractive()}>Interactive Map</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateStatic()}>Static Map</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateDataTable()}>Data Table</div>
                    </button>
                </div>
                <div>
                    {this.state.content}
                </div>
            </div>
        )
    }
}

export default VaccinationsCompletedMap