import React, { Component } from 'react'
import MaterialTable from 'material-table'
import Papa from 'papaparse'

const columns = [
    {
        title: 'Identity',
        field: 'Identity',
    },
    {
        title: 'Cummulative Initiated Vaccinations',
        field: 'Cummulative Initiated Vaccinations',
    },
    {
        title: 'Intiated Rate per 1,000',
        field: 'Intiated Rate per 1,000',
        filtering: false,
    },
    {
        title: 'Cummulative Completed Vaccinations',
        field: 'Cummulative Completed Vaccinations',
    },
    {
        title: 'Completed Rate per 1,000',
        field: 'Completed Rate per 1,000',
        filtering: false,
    },
]

class DisparitiesVaccinationsDataTable extends Component {

    constructor(props) {
        super(props)
        this.state = {
            data: []
        }
        this.updateData = this.updateData.bind(this)
    }

    updateData(result) {
        let y = [];
        const results = result.data
        for (let i = 0; i < results.length - 1; i++) {
            y.push({
                "Identity": results[i]['value'],
                "Cummulative Initiated Vaccinations": results[i]['initiated'],
                "Intiated Rate per 1,000": results[i]['initiated_rate'],
                "Cummulative Completed Vaccinations": results[i]['completed'],
                "Completed Rate per 1,000": results[i]['completed_rate'],
            })
        }
        y.reverse()
        this.setState({ data: y })
    }

    componentDidMount() {
        Papa.parse('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/d4d5f6d107614c509a0dc2b9c9ca9164d75f3e5f/data/individual/mo_vaccine_race_rates.csv', {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    componentWillUnmount() {
        this.setState({ data: [] })
    }

    render() {
        return (
            <div style={{ width: '100%' }}>
                <MaterialTable
                    title="Disparities Vaccinations"
                    columns={columns}
                    data={this.state.data}
                    options={{
                        search: true,
                        paging: true,
                        header: true,
                        filtering: true,
                        /*headerStyle: {
                            backgroundColor: '#01579b',
                            color: '#FFF'
                        },*/
                    }}
                />
            </div>
        )
    }
}

export default DisparitiesVaccinationsDataTable