import React, { Component } from 'react'
import MaterialTable from 'material-table'
import Papa from 'papaparse'

class CasesCountyData extends Component {

    constructor(props) {
        super(props)
        this.state = {
            content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_midmo/b_case_rate.png' />, //mid-missouri
        }
    }

    updateMid() {
        this.setState({ content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_midmo/b_case_rate.png' /> })
    }
    updateN() {
        this.setState({ content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_nomo/b_case_rate.png' /> })
    }
    updateNE() {
        this.setState({ content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_nemo/b_case_rate.png' /> })
    }
    updateNW() {
        this.setState({ content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_stjo/b_case_rate.png' /> })
    }
    updateSC() {
        this.setState({ content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_ozark/b_case_rate.png' /> })
    }
    updateSE() {
        this.setState({ content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_semo/b_case_rate.png' /> })
    }
    updateSW() {
        this.setState({ content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_swmo/b_case_rate.png' /> })
    }
    updateWC() {
        this.setState({ content: <img src='https://slu-opengis.github.io/covid_daily_viz/img/county_west/b_case_rate.png' /> })
    }

    render() {
        return (
            <div>
                <div className='buttonContainer' style={{ width: 'max-content' }}>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateMid()}>Mid</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateN()}> North</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateNE()}> NorthE</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateNW()}> NorthW</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateSC()}> South</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateSE()}> SouthE</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateSW()}> SouthW</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateWC()}> West</div>
                    </button>
                </div>
                <div style={{ marginLeft: 'auto' }}>
                    {this.state.content}
                </div>
            </div>
        )
    }
}

export default CasesCountyData