import { HashLink as Link } from 'react-router-hash-link';
import React from 'react';
import './css/Faq.css'

function Faq() {
    return (
        <div className="faq">
            <nav className="faqNav">
                <ul className="faqNav-menu">
                    <li className="faqNav-item menuHeader">
                        Navigator
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#where">Where Do These Data Come From?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#why">Why Do Numbers Not Match?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#pCases">What Are Probable Cases?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#pRates">What Are Per Capita Rates?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#lPlots">What Are Log Plots?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#rAvg">What Are Rolling Average?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#hData">Why Do You Not Have Hospitalization Data for Missouri?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#counties">Why Do You Treat Joplin and Kansas City as Counties?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#met">What Is a Metropolitan Statistical Area?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#rData">Can I Reuse Your Data or Graphics?</Link>
                    </li>
                    <li className="faqNav-item">
                        <Link className="faqNav-link" to="/faq/#dData">How Do I Download Your Data?</Link>
                    </li>
                    <li className="faqNav-item" id="bottomItem">
                        <Link className="faqNav-link" to="/faq/#dGraphics">How Do I Download Your Graphics?</Link>
                    </li>
                </ul>
            </nav>
            <h1 className="sectionTitle" id="intro">Frequently Asked Questions</h1>
            <section className="segments">
                <div className="container" id="where">
                    <h2 className="sectionTitle">Where Do These Data Come From?</h2>
                    <div className="textBox">
                        <p>
                            Data for this site is compiled from a number of sources on a daily basis:
                        </p>
                        <p>
                            <ol>
                                <li>County and state COVID-19 data - the <a className="faqLink" href="https://nytimes.com">New York Times</a> <a className="faqLink" href="https://github.com/nytimes/covid-19-data">COVID-19 Project</a></li>
                                <li>State health disparities data - the <a className="faqLink" href="https://health.mo.gov/living/healthcondiseases/communicable/novel-coronavirus/">Missouri Department of Health and Senior Services</a></li>
                                <li>St. Louis area health disparities data - the <a className="faqLink" href="https://www.stlouis-mo.gov/covid-19/data/index.cfm">City of St. Louis</a> and <a className="faqLink" href="https://stlcorona.com/resources/covid-19-statistics1/">St. Louis County</a></li>
                                <li>St. Louis area zip code data:
                                    <ul>
                                        <li><a className="faqLink" href="https://www.franklinmo.org/covid19">Franklin County</a></li>
                                        <li><a className="faqLink" href="https://www.jeffcohealth.org/covid19-data">Jefferson County</a></li>
                                        <li><a className="faqLink" href="https://experience.arcgis.com/experience/61207258c1f243cabcec743c23064fa6">Lincoln County</a></li>
                                        <li><a className="faqLink" href="https://www.sccmo.org/2105/COVID-19">St. Charles County</a></li>
                                        <li><a className="faqLink" href="https://www.stlouis-mo.gov/covid-19/data/index.cfm">St. Louis City</a></li>
                                        <li><a className="faqLink" href="https://stlcorona.com/resources/covid-19-statistics1/">St. Louis County</a></li>
                                        <li><a className="faqLink" href="https://www.dph.illinois.gov/covid19/covid19-statistics" >State of Illinois</a></li>
                                        <li><a className="faqLink" href="https://www.warrencountyhealth.com/covid-19-dashboard/" >Warren County</a></li>
                                    </ul>
                                </li>
                                <li>St. Louis area hospitalization data - the <a className="faqLink" className="faqLink" href="https://www.facebook.com/StLouisMetropolitanPandemicTaskForce/">St. Louis Metropolitan Pandemic Task Force</a></li>
                                <li>Kansas City area zip code data:
                                    <ul>
                                        <li><a className="faqLink" href="https://experience.arcgis.com/experience/34f9ef5e486b4ef3a0a1364c457944bb/page/page_1/">Clay County</a></li>
                                        <li><a className="faqLink" href="https://jacohd.org/data-dashboard/">Jackson County</a></li>
                                        <li><a className="faqLink" href="https://viz.jocogov.org/t/Public/views/JohnsonCountyKS-COVID-19Update/Dashboard?:isGuestRedirectFromVizportal=y&amp;:embed=y">Johnson County</a></li>
                                        <li><a className="faqLink" href="https://data.kcmo.org/Health/COVID-19-Data-by-ZIP-Code/374j-h7xt">Kansas City</a></li>
                                        <li><a className="faqLink" href="https://www.plattecountyhealthdept.com/emergency.aspx">Platte County</a></li>
                                        <li><a className="faqLink" href="https://wyandotte-county-covid-19-hub-unifiedgov.hub.arcgis.com">Wyandotte County</a></li>
                                    </ul>
                                </li>
                                <li>Demographic data - the U.S. Census Bureaus American Community Survey 5-year estimates (2014-2018)</li>
                            </ol>
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="why">Why Do Numbers Not Match?</h2>
                    <div className="textBox">
                        <p>
                            Due to lags in reporting, both at the public health department level and at the New York Times itself, case and mortality numbers may lag behind other figures reported in the media. At the same time, because the New York Times obtains data from multiple sources, some case and mortality numbers may actually be higher than what is reported by the state.
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="pCases">What Are Probable Cases?</h2>
                    <div className="textBox">
                        <p>
                            On May 6th, the New York Times began including probable cases - those without a confirmatory test result but where symptoms indicate a COVID-19 infection. Not all counties report probable cases, however.
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="pRates">What Are Per Capita Rates?</h2>
                    <div className="textBox">
                        <p>
                            In order to compare rates across neighborhoods, cities, states, or countries, use utilize per capita statistics. When we calculate a rate per capita or a per capita rate, we take our rate and multiply it by a set number of individuals. This helps us ensure that our data are expressed relative to the size of the population they describe. For example, we typically calculate infant mortality rate by multiplying a given jurisdictions rate by 1,000. In the United States, we typically calculate per capita crime rates by taking a citys or states rate, and multiplying it by 100,000.
                        </p>
                        <p>
                            Calculating rates is particularly important for mapping these quantities. This is sometimes called normalization by cartographers. By ensuring that we adjust the values mapped for the number of people in a given area, we ensure that our map is not just reflecting variations in population between jurisdictions.
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="lPlots">What Are Log Plots?</h2>
                    <div className="textBox">
                        <p>
                            The website Datawrapper has a <a className="faqLink" href="https://blog.datawrapper.de/weeklychart-logscale/">great explainer</a> on how to read log plots. The key takeaway from their explanation is that Log scales show relative values instead of absolute ones. Instead of showing absolute change, they show us the <em>rate</em> at which change happens. These are useful for comparing how COVID-19, for example, spreads between multiple counties. The steeper the line for a particular jurisdiction, the faster the rate of growth.
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="rAvg">What Are Rolling Average?</h2>
                    <div className="textBox">
                        <p>
                            A rolling average is a technique for calculating an average for values for different windows. All of the plots on this site use a seven day rolling average, meaning that the the value of a given days average is calculated from the prior seven days values. This has the effect of smoothing out large daily variations, for example, in the number of new COVID cases or hospitalizations reported. Doing so gives us a better sense of the trend - if cases climbing or falling, for example.
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="hData">Why Do You Not Have Hospitalization Data for Missouri?</h2>
                    <div className="textBox">
                        <p>
                            Missouri does not have what Chris considers reliable statewide hospitalization data. Their partners, the Missouri Hospital Association (MHA), provide the state with hospitalization data from the Centers for Disease Controls National Healthcare Safety Network (NHSN). Unfortunately, not all hospitals in Missouri report to the NHSN each day. MHA has stopped reporting the exact number of hospitals reporting daily, which had been their practice until early June. Since the number of hospitals reporting changes each day, it is impossible to disentangle whether COVID-19 hospitalizations went up because more hospitals were reporting or because there was a genuine increase in the number of in-patients in Missouri. Likewise, it is impossible to know if decreases are genuine or are artifact from a smaller number of hospitals reporting.
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="counties">Why Do You Treat Joplin and Kansas City as Counties?</h2>
                    <div className="textBox">
                        <p>
                            Since both cities have their own health departments, and straddle multiple counties, both the State of Missouri and the New York Times have been releasing data on infections and deaths exclusive of the counties they lie in. This has been true for Kansas City since the beginning of the pandemic, and true for Joplin since June 25th. I have adjusted the populations for both the cities themselves and the surrounding counties to provide accurate per capita rates, and have also created maps that show Joplin and Kansas City as their own entities.
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="met">What Is a Metropolitan Statistical Area?</h2>
                    <div className="textBox">
                        <p>
                            A Metropolitan Statistical Area, or a MSA, is a region defined by the U.S. Office of Management and Budget with the U.S. Census Bureau:
                        </p>
                        <blockquote>
                            <p>
                                The general concept of a metropolitan or micropolitan statistical area is that of a core area containing a substantial population nucleus, together with adjacent communities having a high degree of economic and social integration with that core.
                            </p>
                        </blockquote>
                        <p>
                            You can read more about MSAs on the <a className="faqLink" href="https://www.census.gov/programs-surveys/metro-micro/about.html" >Census Bureaus website</a>.
                        </p>
                        <p>
                            This site uses these pre-existing definitions for all data involving metro areas, with the county-equivalents for <a className="faqLink" href="#Why_Do_You_Treat_Joplin_and_Kansas_City_as_Counties">Joplin and Kansas City</a> added to their respective metros. There are a total of eight MSAs in Missouri, several of which include counties in adjacent states:
                        </p>
                        <ol className="adjCounties">
                            <li>Cape Girardeau, MO-IL Metro Area</li>
                            <li>Columbia, MO Metro Area</li>
                            <li>Jefferson City, MO Metro Area</li>
                            <li>Joplin, MO Metro Area</li>
                            <li>Kansas City, MO-KS Metro Area</li>
                            <li>Springfield, MO Metro Area</li>
                            <li>St. Joseph, MO-KS Metro Area</li>
                            <li>St. Louis, MO-IL Metro Area</li>
                        </ol>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="rData">Can I Reuse Your Data or Graphics?</h2>
                    <div className="textBox">
                        <p>
                            Absolutely! All code, data, website content, and images are available under a CC-BY 4.0 license. This means that you can use these data and graphics as long as you tell your audience where they came from. Graphics should not be altered to remove the caption that indicates who made them and where the underlying data came from.
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="dData">How Do I Download Your Data?</h2>
                    <div className="textBox">
                        <p>
                            The easiest way to download them is to go to the <a className="faqLink" href="https://github.com/slu-openGIS/covid_daily_viz">GitHub repository</a> for this project and click on the green <code>Code</code> button. The menu that appears will give you an option to download a <code>.zip</code> file that contains all of the clean data and most of the raw data (in the <code>data/</code> folder).
                        </p>
                    </div>
                </div>
            </section>
            <section className="segments">
                <div className="container">
                    <h2 className="sectionTitle" id="dGraphics" >How Do I Download Your Graphics?</h2>
                    <div className="textBox">
                        <p>
                            Low resolution version of the graphics can be downloaded one at a time from the website by right clicking on them. If you want to download them all at once, or download the high resolution versions, follow the same process for downloading the data from <a className="faqLink" href="https://github.com/slu-openGIS/covid_daily_viz">GitHub</a> (see above). The images will be in the <code>results/</code> folder.
                        </p>
                    </div>
                </div>
            </section>
        </div>
    );
}

export default Faq;
