import React, { Component } from 'react'
import Papa from 'papaparse'
import { Chart } from 'react-chartjs-2'

let disparitiesVaccinationsChart;

let ti = []
let ri = []
let tc = []
let rc = []

let labels = ['Native', 'Asian', 'Black', 'Two or More', 'Pacific Islander', 'White', 'Latino']

function all(results) {
    for (let i = 0; i < results.length - 1; i++) {
        ti.push(results[i]['initiated'])
        ri.push(results[i]['initiated_rate'])
        tc.push(results[i]['completed'])
        rc.push(results[i]['completed_rate'])
    }
}

function parseData(url) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            all(results.data)
            disparitiesVaccinationsChart.update()
        }
    });
}

function switchTI() {
    disparitiesVaccinationsChart.destroy()
    options['title']['text'] = 'Total Initiated'
    data['datasets'][0]['data'] = ti
    let newChart = new Chart("disparitiesVaccinationsChart", {
        type: 'bar',
        data: data,
        options: options
    })
    disparitiesVaccinationsChart = newChart
}

function switchRI() {
    disparitiesVaccinationsChart.destroy()
    options['title']['text'] = 'Initiated Rate per 1,000'
    data['datasets'][0]['data'] = ri
    let newChart = new Chart("disparitiesVaccinationsChart", {
        type: 'bar',
        data: data,
        options: options
    })
    disparitiesVaccinationsChart = newChart
}

function switchTC() {
    disparitiesVaccinationsChart.destroy()
    options['title']['text'] = 'Total Completed'
    data['datasets'][0]['data'] = tc
    let newChart = new Chart("disparitiesVaccinationsChart", {
        type: 'bar',
        data: data,
        options: options
    })
    disparitiesVaccinationsChart = newChart
}

function switchRC() {
    disparitiesVaccinationsChart.destroy()
    options['title']['text'] = 'Completed Rate per 1,000'
    data['datasets'][0]['data'] = rc
    let newChart = new Chart("disparitiesVaccinationsChart", {
        type: 'bar',
        data: data,
        options: options
    })
    disparitiesVaccinationsChart = newChart
}

var data = {
    labels: labels,
    datasets: [
        {
            label: 'Identities',
            data: ti,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(153, 102, 255, 1)'
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(153, 102, 255, 1)'
            ],

            borderWidth: 1,
        },
    ]
}

var options = {
    responsiveAnimationDuration: 1050,
    title: {
        display: true,
        text: 'Total Initiated',
        fontSize: 30,
    },

    legend: {
        labels: {
            fontSize: 15,
        }
    },
    responsive: true,
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    fontSize: 18,
                    beginAtZero: true,
                },
            },
        ],

        xAxes: [
            {
                ticks: {
                    fontSize: 18,
                }
            },
        ]
    },
}

class DisparitiesVaccinationsChart extends Component {

    componentDidMount() {
        disparitiesVaccinationsChart = new Chart("disparitiesVaccinationsChart", {
            type: 'bar',
            data: data,
            options: options
        })
        parseData('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/d4d5f6d107614c509a0dc2b9c9ca9164d75f3e5f/data/individual/mo_vaccine_race_rates.csv')
    }

    componentWillUnmount() {
        disparitiesVaccinationsChart.destroy()
        ti = []
        ri = []
        tc = []
        rc = []
    }


    render() {
        return (
            <div>
                <div className="buttonContainer">
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchTI()}>Total Initiated</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchRI()}>Initiated Rate</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchTC()}>Total Completed</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchRC()}>Completed Rate</div>
                    </button>
                </div>
                <div className="chart">
                    <canvas id="disparitiesVaccinationsChart" style={{ width: 600, height: 500, backgroundColor: "white", marginTop: 30, borderRadius: 8 }}></canvas>
                </div>
            </div>
        )
    }
}

export default DisparitiesVaccinationsChart