import React, { Component } from 'react';
import VaccinationsInitiatedMap from '../components/VaccinationsInitiatedMap'
import VaccinationsCompletedMap from '../components/VaccinationsCompletedMap'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import './css/Data.css'
import './css/DataHub.css'

class VaccinationsGeneralTrends extends Component {

    componentDidMount() {
        window.scrollTo(0, 0)
    }

    render() {
        return (
            <motion.div className="data home"
                initial={{ opacity: 0.75, x: -200 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.8 }}
                exit={{ opacity: 0 }}>
                <link rel="stylesheet" href="https://fonts.googleapis.com/icon?family=Material+Icons"></link>
                <div className="card-container">
                    <ul className="card-list">
                        <lh className="card-header">Vaccinations</lh>
                        <li className="pushable return">
                            <Link to="/datahub">
                                <div className="shadow"></div>
                                <div className="front">Data Hub</div>
                            </Link>
                        </li>
                    </ul>
                </div>
                <div className="bigContainer">
                    <h2 style={{ color: 'white', alignSelf: 'center', marginTop: 30 }}>Vaccinations Initiated per County</h2>
                    <div className="chartContainer">
                        <VaccinationsInitiatedMap />
                    </div>
                </div>
                <div className="bigContainer">
                    <h2 style={{ color: 'white', alignSelf: 'center', marginTop: 30 }}>Vaccinations Completed per County</h2>
                    <div className="chartContainer">
                        <VaccinationsCompletedMap />
                    </div>
                </div>
                <div className="bigContainer footer">
                    <p>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
                </p>
                </div>
            </motion.div >
        )
    }
}

export default VaccinationsGeneralTrends