import React from 'react';
import { HashLink as Link } from 'react-router-hash-link';
import HomeDataTable from "../components/HomeDataTable"
import InfectionsMap from '../components/InfectionsMap'
import NewCasesMap from '../components/NewCasesMap'
import MortalityMap from '../components/MortalityMap'
import { motion } from 'framer-motion'
import './css/Home.css'

function Home() {
    return (
        <motion.div className='home'
            initial={{ opacity: 0.75, x: -200 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8 }}
            exit={{ opacity: 0 }}>
            <link rel="stylesheet" href="https://fonts.googleapis.com/icon?family=Material+Icons"></link>
            <section className="segments">
                <nav className="homeNav">
                    <ul className="homeNav-menu">
                        <li className="homeNav-item menuHeader">
                            Navigator
                    </li>
                        <li className="homeNav-item">
                            <Link className="homeNav-link" to="/#intro">Overview</Link>
                        </li>
                        <li className="homeNav-item">
                            <Link className="homeNav-link" to="/#dailyData">Daily Data</Link>
                        </li>
                        <li className="homeNav-item">
                            <Link className="homeNav-link" to="/#inFrates">Infection Rates</Link>
                        </li>
                        <li className="homeNav-item">
                            <Link className="homeNav-link" to="/#avgCases">Average Cases</Link>
                        </li>
                        <li className="homeNav-item" id="bottomItem">
                            <Link className="homeNav-link" to="/#moRates">Missouri Rates</Link>
                        </li>
                    </ul>
                </nav>
                <div className="container">
                    <h1 className="sectionTitle" id="intro"> Overview </h1>
                    <div className="textBox">
                        <p>
                            Welcome to <a className="homeLink" href="https://chris-prener.github.io/">Chris Preners</a> tracking site for the SARS-CoV-2 pandemic in Missouri.
                        his site is updated nightly using data from a variety of federal, state, and local data sources as well as the <a className="homeLink" href="https://nytimes.com/">New York Times</a> <a className="homeLink" href="https://github.com/nytimes/covid-19-data">COVID-19 Project</a>.
                        Chris also posts <a className="homeLink" href="https://twitter.com/chrisprener">Twitter</a> threads on Tuesday and Thursday evenings breaking down current trends in Missouri. If you are new to the site, please read the <a className="homeLink" href="/about">About</a> page for an overview of these data.
                    </p>
                        <p>
                            Chris also emails out a weekly round-up of COVID-19 trends for Missouri. These weekly newsletters, named River City Data, also feature some exclusive content, including short interviews with COVID-19 experts, health care providers, and others who have insight into the pandemic.
                    </p>
                    </div>
                </div>
            </section>

            <section className="segments" id="special">
                <div className="container" id="dailyData">
                    <h2 className="sectionTitle">Daily Data</h2>
                    <div className="textBox">
                        <p>
                            While the <a className="homeLink" href="https://www.stlouis-mo.gov/covid-19/data/">City of St. Louis</a>, <a className="homeLink" href="https://stlcogis.maps.arcgis.com/apps/MapSeries/index.html?appid=6ae65dea4d804f2ea4f5d8ba79e96df1">St. Louis County</a>, and <a className="homeLink" href="https://www.kcmo.gov/city-hall/departments/health/coronavirus-covid-19/covid-19-totals">Kansas City</a> provide day-by-day tracking of cumulative cases on their respective dashboards, the State of Missouri does not. The following tabs provide daily breakdowns of COVID data as well as additional statistics not included in the existing dashboards. Data are also provided for the wider St. Louis and Kansas City metro areas. Please note that the two average columns for reported cases and deaths are both seven-day rolling averages.
                        </p>
                    </div>
                    <div className="dataTableContainer">
                        <HomeDataTable />
                    </div>
                </div>
            </section>

            <section className="segments">
                <div className="container" id="inFrates">
                    <h2 className="sectionTitle">Infection Rates by County</h2>
                    <div className="textBox">
                        <p>
                            This map shows infections as a rate per 1,000 residents. It is important not to map the raw counts themselves, but if you want to see those data, click on a county.
                        </p>
                    </div>
                    <InfectionsMap />
                </div>
            </section>


            <section className="segments">
                <div className="container" id="avgCases">
                    <h2 className="sectionTitle">Average New Cases by County</h2>
                    <div className="textBox">
                        <p>
                            This map shows a seven-day rolling average of new cases.
                            For this map, this covers 2021-03-07 back through 2021-02-28.
                            There is not a threshold for what constitutes a high or low average, but the higher the average number of new cases, the more new spread we can infer.
                            For mapping purposes, these are displayed as a rate per 100,000 residents.
                            As with the prior map, additional details are available by clicking on each county or on the data table.
                        </p>
                    </div>
                    <NewCasesMap />
                </div>
            </section>


            <section className="segments">
                <div id="moRates" className="container">
                    <h2 className="sectionTitle">Mortality Rates by County</h2>
                    <div className="textBox">
                        <p>
                            This map shows mortality as a rate per 1,000 residents. As with the prior maps, raw counts of deaths and hospital infrastructure details are available by clicking on individual counties or on the data table.
                        </p>
                    </div>
                    <MortalityMap />
                </div>
            </section>
        </motion.div >
    );
}

export default Home;
