import React, { Component } from 'react'
import { Chart } from 'react-chartjs-2'
import Papa from 'papaparse'

let divisionChart;

let divisionLabels = ['New England', 'Mid-Atlantic', 'East North Central', 'West North Central', 'South Atlantic', 'East South Central', 'West South Central', 'Mountain', 'Pacific'];
let neCases = [0];
let maCases = [0];
let encCases = [0];
let wncCases = [0];
let saCases = [0];
let escCases = [0];
let wscCases = [0];
let mountainCases = [0];
let pacificCases = [0];

function doStuff(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['state'] === 'Connecticut' || data[i]['state'] === 'Maine' || data[i]['state'] === 'Massachusetts' || data[i]['state'] === 'New Hampshire' || data[i]['state'] === 'Rhode Island' || data[i]['state'] === 'Vermont') {
            neCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'New Jersey' || data[i]['state'] === 'New York' || data[i]['state'] === 'Pennsylvania') {
            maCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Illinois' || data[i]['state'] === 'Indiana' || data[i]['state'] === 'Michigan' || data[i]['state'] === 'Ohio' || data[i]['state'] === 'Wisconsin') {
            encCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Iowa' || data[i]['state'] === 'Kansas' || data[i]['state'] === 'Minnesota' || data[i]['state'] === 'Missouri' || data[i]['state'] === 'Nebraska' || data[i]['state'] === 'North Dakota' || data[i]['state'] === 'South Dakota') {
            wncCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Delaware' || data[i]['state'] === 'Florida' || data[i]['state'] === 'Georgia' || data[i]['state'] === 'Maryland' || data[i]['state'] === 'North Carolina' || data[i]['state'] === 'South Carolina' || data[i]['state'] === 'Virginia' || data[i]['state'] === 'District of Columbia' || data[i]['state'] === 'West Virginia') {
            saCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Alabama' || data[i]['state'] === 'Kentucky' || data[i]['state'] === 'Mississippi' || data[i]['state'] === 'Tennessee') {
            escCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Arkansas' || data[i]['state'] === 'Louisiana' || data[i]['state'] === 'Oklahoma' || data[i]['state'] === 'Texas') {
            wscCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Arizona' || data[i]['state'] === 'Colorado' || data[i]['state'] === 'Idaho' || data[i]['state'] === 'Montana' || data[i]['state'] === 'Nevada' || data[i]['state'] === 'New Mexico' || data[i]['state'] === 'Utah' || data[i]['state'] === 'Wyoming') {
            mountainCases[0] += data[i]['cases']
        }
        if (data[i]['state'] === 'Alaska' || data[i]['state'] === 'California' || data[i]['state'] === 'Hawaii' || data[i]['state'] === 'Oregon' || data[i]['state'] === 'Washington') {
            pacificCases[0] += data[i]['cases']
        }
    }
}

function parseData(url, callBack) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            callBack(results.data);
            divisionChart.update()
        }
    });
}

let data = {
    labels: divisionLabels,
    datasets: [
        {
            label: 'Cases in Divisions (Schools)',
            data: [neCases, maCases, encCases, wncCases, saCases, escCases, wscCases, mountainCases, pacificCases],

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(192, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(192, 192, 192, 1)'
            ],

            borderWidth: 1,
        },
    ],
}

let options = {
    title: {
        display: true,
        text: 'Cases Based by Divisions (Schools)',
        fontSize: 30
    },
    legend: {
        labels: {
            fontSize: 15,
            /*fontColor: "rgba(255,255,255,0.65)",*/
        }
    },
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    fontSize: 18,
                    beginAtZero: true,
                    display: false,
                },
            },
        ],
        xAxes: [
            {
                ticks: {
                    fontSize: 14,
                    min: 0,
                    display: false,
                },
            },
        ],
    },
}

class DivisionUniversitiesChart extends Component {

    componentDidMount() {
        divisionChart = new Chart("divisionChart", {
            type: 'pie',
            data: data,
            options: options
        })
        parseData('https://raw.githubusercontent.com/nytimes/covid-19-data/master/colleges/colleges.csv', doStuff);
    }

    componentWillUnmount() {
        divisionChart.destroy()
        neCases = [0];
        maCases = [0];
        encCases = [0];
        wncCases = [0];
        saCases = [0];
        escCases = [0];
        wscCases = [0];
        mountainCases = [0];
        pacificCases = [0];
    }

    render() {
        return (
            <div>
                <canvas id="divisionChart" style={{ width: 600, height: 500, backgroundColor: "white", borderRadius: 8 }}></canvas>
            </div >

        )
    }
}

export default DivisionUniversitiesChart