import React, { Component } from 'react'
import Papa from 'papaparse'
import { Chart } from 'chart.js';

let caseChart;

let dates = [];

let moCasesArray = [];
let ilCasesArray = [];
let akCasesArray = [];
let ksCasesArray = [];
let okCasesArray = [];

let moNewCasesArray = [];
let ilNewCasesArray = [];
let akNewCasesArray = [];
let ksNewCasesArray = [];
let okNewCasesArray = [];


function Do(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['state'] === 'Missouri') {
            moCasesArray.push(data[i]['cases'])
            dates.push(data[i]['report_date'])
        }
        if (data[i]['state'] === 'Illinois') {
            ilCasesArray.push(data[i]['cases'])
        }
        if (data[i]['state'] === 'Arkansas') {
            akCasesArray.push(data[i]['cases'])
        }
        if (data[i]['state'] === 'Kansas') {
            ksCasesArray.push(data[i]['cases'])
        }
        if (data[i]['state'] === 'Oklahoma') {
            okCasesArray.push(data[i]['cases'])
        }
    }
}

function woo(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['state'] === 'Missouri') {
            if (i == 2048) {
                moNewCasesArray.push(0)  //this is currently an extreme outlier, not sure what to do
                continue;
            }
            moNewCasesArray.push(data[i]['new_cases'])
            dates.push(data[i]['report_date'])
        }
        if (data[i]['state'] === 'Illinois') {
            ilNewCasesArray.push(data[i]['new_cases'])
        }
        if (data[i]['state'] === 'Arkansas') {
            akNewCasesArray.push(data[i]['new_cases'])
        }
        if (data[i]['state'] === 'Kansas') {
            ksNewCasesArray.push(data[i]['new_cases'])
        }
        if (data[i]['state'] === 'Oklahoma') {
            okNewCasesArray.push(data[i]['new_cases'])
        }
    }
}

function switchNewCases() {
    caseChart.destroy()
    options['title']['text'] = 'New Cases in Different States'
    data['datasets'][0]['data'] = moNewCasesArray
    data['datasets'][1]['data'] = ilNewCasesArray
    data['datasets'][2]['data'] = akNewCasesArray
    data['datasets'][3]['data'] = ksNewCasesArray
    data['datasets'][4]['data'] = okNewCasesArray
    let newChart = new Chart('caseChart', {
        type: 'line',
        data: data,
        options: options
    })
    caseChart = newChart
}

function switchTotalCases() {
    caseChart.destroy()
    options['title']['text'] = 'Cases in Different States'
    data['datasets'][0]['data'] = moCasesArray
    data['datasets'][1]['data'] = ilCasesArray
    data['datasets'][2]['data'] = akCasesArray
    data['datasets'][3]['data'] = ksCasesArray
    data['datasets'][4]['data'] = okCasesArray
    let newChart = new Chart('caseChart', {
        type: 'line',
        data: data,
        options: options,
    })
    caseChart = newChart
}

function parseData(url) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            Do(results.data);
            woo(results.data)
            caseChart.update()
        }
    });
}

let data = {
    labels: dates,
    datasets: [
        {
            label: 'Total Cases in Missouri',
            data: moCasesArray,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
            ],

            borderWidth: 1,
        },

        {
            label: "Total Cases in Illinois",
            data: ilCasesArray,

            backgroundColor: [
                'rgba(54, 162, 235, 0.2)'
            ],

            borderColor: [
                'rgba(54, 162, 235, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "Total Cases in Arkansas",
            data: akCasesArray,

            backgroundColor: [
                'rgba(255, 206, 86, 0.2)'
            ],

            borderColor: [
                'rgba(255, 206, 86, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "Total Cases in Kansas",
            data: ksCasesArray,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "Total Cases in Oklahoma",
            data: okCasesArray,

            backgroundColor: [
                'rgba(153, 102, 255, 0.2)'
            ],

            borderColor: [
                'rgba(153, 102, 255, 1)'
            ],

            borderWidth: 1,


        }
    ],
}

let options = {
    elements: {
        point: {
            radius: 1
        }
    },
    legend: {
        labels: {
            fontSize: 15,
            /*fontColor: "rgba(255,255,255,0.65)",*/
        }
    },
    responsiveAnimationDuration: 1050,
    title: {
        display: true,
        text: 'Cases in Different States',
        fontSize: 30
    },
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    beginAtZero: true,
                    fontSize: 18,
                },
            },
        ],
        xAxes: [{
            ticks: {
                fontSize: 18,
            },
            type: 'time',
            time: {
                unit: 'month',
                unitStepSize: 1,
                displayFormats: {
                    'month': 'MMM YYYY',
                }
            }
        }],
    },
}

class CaseChart extends Component {

    componentDidMount() {
        caseChart = new Chart("caseChart", {
            type: 'line',
            data: data,
            options: options,
        })
        parseData('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/master/data/state/state_full.csv')
    }

    componentWillUnmount() {
        caseChart.destroy()
        dates = [];
        moCasesArray = [];
        ilCasesArray = [];
        akCasesArray = [];
        ksCasesArray = [];
        okCasesArray = [];
        moNewCasesArray = [];
        ilNewCasesArray = [];
        akNewCasesArray = [];
        ksNewCasesArray = [];
        okNewCasesArray = [];
    }

    render() {
        return (
            <div style={{ position: "relative", height: 650 }}>
                <div className="buttonContainer">
                    <button className="pushable" onClick={() => switchTotalCases()}>
                        <div className="shadow"></div>
                        <div className="front">Total Infection</div>
                    </button>
                    <button className="pushable" onClick={() => switchNewCases()}>
                        <div className="shadow"></div>
                        <div className="front">New Cases</div>
                    </button>
                </div>
                <div className="chart">
                    <canvas id="caseChart" style={{ width: 600, height: 500, backgroundColor: "white", marginTop: 30, borderRadius: 8 }} ></canvas>
                </div>
            </div >
        )
    }
}

export default CaseChart