import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom'

import NavBar from './components/Navbar'
import Home from './pages/Home'
import DataHub from './pages/DataHub'
import About from './pages/About'
import Team from './pages/Team'
import Faq from './pages/Faq'
import Universities from './pages/Universities';
import CasesState from './pages/CasesState';
import CasesCounty from './pages/CasesCounty'
import CasesMetro from './pages/CasesMetro'
import DeathsState from './pages/DeathsState'
import DeathsMetro from './pages/DeathsMetro'
import DisparitiesState from './pages/DisparitiesState'
import DisparitiesCountyCity from './pages/DisparitiesCountyCity'
import DisparitiesVaccinations from './pages/DisparitiesVaccinations'
import VaccinationsGeneralTrends from './pages/VaccinationsGeneralTrends'

class App extends Component {

  render() {

    return (
      <div>
        <Router>
          <NavBar />
          <Switch>
            <Route path='https://realericquach.github.io/covid_dashboard/' exact component={Home} />
            <Route path='/datahub' exact component={DataHub} />
            <Route path='/universities' exact component={Universities} />
            <Route path='/about' exact component={About} />
            <Route path='/team' exact component={Team} />
            <Route path='/faq' exact component={Faq} />
            <Route path='/experiment/casesstate' exact component={CasesState} />
            <Route path='/experiment/casesmetro' exact component={CasesMetro} />
            <Route path='/experiment/casesCounty' exact component={CasesCounty} />
            <Route path='/experiment/disparitiesState' exact component={DisparitiesState} />
            <Route path='/experiment/disparitiesCountyCity' exact component={DisparitiesCountyCity} />
            <Route path='/experiemnt/disparitiesVaccinations' exact component={DisparitiesVaccinations} />
            <Route path='/experiment/deathsExcess' exact component={DeathsState} />
            <Route path='/experiment/deathsState' exact component={DeathsState} />
            <Route path='/experiment/deathsMetro' exact component={DeathsMetro} />
            <Route path='/experiment/vaccinationsGeneral' exact component={VaccinationsGeneralTrends} />
          </Switch>
        </Router>
      </div>
    );
  }
}

export default App