import React, { Component } from 'react'
import MaterialTable from 'material-table'
import Papa from 'papaparse'

const columns = [
    {
        title: 'Identity',
        field: 'Identity',
    },
    {
        title: 'Cummulative Cases',
        field: 'Cummulative Cases',
    },
    {
        title: 'Cases per Capita',
        field: 'Cases per Capita',
        filtering: false,
    },
    {
        title: 'Cummulative Deaths',
        field: 'Cummulative Deaths',
    },
    {
        title: 'Deaths per Capita',
        field: 'Deaths per Capita',
        filtering: false,
    },
]

class DisparitiesVaccinationsDataTable extends Component {

    constructor(props) {
        super(props)
        this.state = {
            data: []
        }
        this.updateData = this.updateData.bind(this)
    }

    updateData(result) {
        let y = [];
        const results = result.data
        for (let i = 0; i < results.length - 4; i++) {
            y.push({
                "Identity": results[i]['value'],
                "Cummulative Cases": Math.round(results[i]['cases_est']),
                "Cases per Capita": results[i]['case_rate'].toFixed(3),
                "Cummulative Deaths": Math.round(results[i]['deaths_est']),
                "Deaths per Capita": results[i]['mortality_rate'].toFixed(3),
            })
        }
        y.reverse()
        this.setState({ data: y })
    }

    componentDidMount() {
        Papa.parse('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/d4d5f6d107614c509a0dc2b9c9ca9164d75f3e5f/data/individual/mo_race_rates.csv', {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    componentWillUnmount() {
        this.setState({ data: [] })
    }

    render() {
        return (
            <div style={{ width: '100%' }}>
                <MaterialTable
                    title="Disparities State"
                    columns={columns}
                    data={this.state.data}
                    options={{
                        search: true,
                        paging: true,
                        header: true,
                        filtering: true,
                        /*headerStyle: {
                            backgroundColor: '#01579b',
                            color: '#FFF'
                        },*/
                    }}
                />
            </div>
        )
    }
}

export default DisparitiesVaccinationsDataTable