import React, { Component } from 'react'
import Papa from 'papaparse'
import MaterialTable from 'material-table'
import '../pages/css/Data.css'

const columns = [
    {
        title: 'State',
        field: 'State',
    },
    {
        title: 'County',
        field: 'County',
    },
    {
        title: 'Cummulative Cases',
        field: 'Cummulative Cases',
        filtering: false,
    },
    {
        title: 'Rate per 1,000 Residents',
        field: 'Rate per 1,000 Residents',
        filtering: false,
    }
]

let formatTwoDigits = (digit) => ("0" + digit).slice(-2);
let today = new Date()
let twodate = today.getFullYear() + '-' + (formatTwoDigits(today.getMonth() + 1)) + '-' + (formatTwoDigits(today.getDate() - 2))
let onedate = today.getFullYear() + '-' + (formatTwoDigits(today.getMonth() + 1)) + '-' + (formatTwoDigits(today.getDate() - 1))
let zerodate = today.getFullYear() + '-' + (formatTwoDigits(today.getMonth() + 1)) + '-' + (formatTwoDigits(today.getDate()))


class InfectionsMap extends Component {

    constructor(props) {
        super(props);
        this.state = {
            content: <iframe className="mapBox" src="https://cs.slu.edu/student/ando/capstone/inf_rates.html" className="home_map" title="map1"></iframe>,
            data: []
        }
        this.updateData = this.updateData.bind(this)
    }

    updateData(result) {
        let y = [];
        const results = result.data
        for (let i = 0; i < results.length - 1; i++) {
            if (results[i]['report_date'] == zerodate || results[i]['report_date'] == onedate || results[i]['report_date'] == twodate) {
                y.push({
                    "State": results[i]['state'],
                    "County": results[i]['county'],
                    "Cummulative Cases": results[i]['cases'],
                    "Rate per 1,000 Residents": results[i]['case_rate'].toFixed(3),
                })
            }
        }
        this.setState({ data: y })
    }

    componentDidMount() {
        Papa.parse('https://git.cs.slu.edu/api/v4/projects/2267/repository/files/site%2Fdata%2Fcounty_full_recent.csv/raw?ref=master&private_token=8yfE4j_YrY2kvypQ8xHc&fbclid=IwAR08kHnG_98YM_Zv-HJtMMexBaz8uZJqcPKjwpYm2DoHdMqHpvGwiDlpPmQ', {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    componentWillUnmount() {
        this.setState({ data: [] })
    }

    updateStatic() {
        this.setState({ content: <img src={'https://slu-opengis.github.io/covid_daily_viz/img/county/a_case_map.png'}></img> })
    }

    updateInteractive() {
        this.setState({ content: <iframe className="mapBox" src="https://cs.slu.edu/student/ando/capstone/inf_rates.html" className="home_map" title="map1"></iframe> })
    }

    updateDataTable() {
        this.setState({
            content:
                <div style={{ marginTop: 50 }}>
                    <MaterialTable
                        title="Infections Map"
                        columns={columns}
                        data={this.state.data}
                        options={{
                            search: true,
                            paging: true,
                            header: true,
                            filtering: true,
                        }}
                    />
                </div>
        })
    }

    render() {
        return (
            <div className='data' style={{ background: 'transparent', width: '100%' }}>
                <div className='buttonContainer'>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateInteractive()}>Interactive Map</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateStatic()}>Static Map</div>
                    </button>
                    <button className='pushable'>
                        <div className='shadow'></div>
                        <div className='front' onClick={() => this.updateDataTable()}>Data Table</div>
                    </button>
                </div>
                <div>
                    {this.state.content}
                </div>
            </div>
        )
    }
}

export default InfectionsMap