class USTimeline extends React.Component {
    constructor(props) {
        super(props);
        this.updateData = this.updateData.bind(this)
        this.state = {
            schema: [{
                "name": "Time",
                "type": "date",
                "format": "%Y-%m-%d"
            }, {
                "name": "Cases",
                "type": "number"
            }],
            data: [],
            timeseriesDs: {
                type: "timeseries",
                renderAt: "container",
                width: "80%",
                height: "25%",
                dataSource,
            }
        };
    }

    componentDidMount() {
        Papa.parse('https://raw.githubusercontent.com/datasets/covid-19/main/data/time-series-19-covid-combined.csv', {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    updateData(results) {
        let cases = [];
        let result = results.data
        for (let i = 0; i < result.length - 1; i++) {
            if (result[i]['Country/Region'] == 'US') {
                cases.push([result[i]['Date'], result[i]['Confirmed']])
            }
        }
        this.setState({ data: cases })
        const fusionTable = new FusionCharts.DataStore().createDataTable(
            this.state.data,
            this.state.schema
        );
        const timeseriesDs = Object.assign({}, this.state.timeseriesDs);
        timeseriesDs.dataSource.data = fusionTable;
        this.setState({
            timeseriesDs
        });
    }

    render() {
        return (
            <div style={{ height: '100%', maxWidth: '100%' }}>
                {this.state.timeseriesDs.dataSource.data ? (
                    <ReactFC {...this.state.timeseriesDs} />
                ) : (
                    ""
                )}
            </div>
        );
    }
}

export default USTimeline