import React from 'react';
import MOUniversitiesChart from '../components/MOUniversitiesChart'
import RegionUniversitiesChart from '../components/RegionUniversitiesChart'
import DivisionUniversitiesChart from '../components/DivisionUniversitiesChart'
import './css/Universities.css'

function Universities() {
    return (
        <div className='universities'>
            <h1>Universities Data Collection</h1>
            <p id='top'>The following data is based upon the reasearch done by the team at The New York Times. The graph below pertains to notable Missouri schools.</p>
            <div className="segment">
                <div className="chartContainer">
                    <MOUniversitiesChart />
                </div>
            </div>
            <div className="segment">
                <div className="circleChart">
                    <RegionUniversitiesChart />
                </div>
                <p>The following data encompasses schools across the US and divides them into four major regions</p>
            </div>
            <div className="segment">
                <p>We further divide the data down into 9 divisions here</p>
                <div className="circleChart">
                    <DivisionUniversitiesChart />
                </div>
            </div>
        </div>
    );
}

export default Universities;