import React from "react";
import FusionCharts from "fusioncharts";
import TimeSeries from "fusioncharts/fusioncharts.timeseries";
import ReactFC from "react-fusioncharts";

ReactFC.fcRoot(FusionCharts, TimeSeries);

const jsonify = res => res.json();
const dataFetch = fetch(
    "https://s3.eu-central-1.amazonaws.com/fusion.store/ft/data/annotating-single-data-point-data.json"
).then(jsonify);
const schemaFetch = fetch(
    "https://s3.eu-central-1.amazonaws.com/fusion.store/ft/schema/annotating-single-data-point-schema.json"
).then(jsonify);

const dataSource = {
    caption: {
        text: "Timeline of COVID-19"
    },
    subcaption: {
        text: "Cases in the US"
    },
    yaxis: [
        {
            plot: "Interest Rate",
            title: "Cases"
        }
    ],
    datamarker: [
        {
            value: "Interest Rate",
            time: "Mar-1980",
            identifier: "H",
            timeformat: "%b-%Y",
            tooltext:
                "As a part of credit control program, under the leadership of Paul Volcker, the Fed tightened the money supply, allowing the federal fund rates to approach 20 percent."
        },
        {
            value: "Interest Rate",
            time: "Aug-1982",
            identifier: "L",
            timeformat: "%b-%Y",
            tooltext:
                "The FED eases off the monetary brakes, allowing interest rates to fall and the economy to begin a strong recovery."
        },
        {
            value: "Interest Rate",
            time: "Oct-1987",
            identifier: "L",
            timeformat: "%b-%Y",
            tooltext: "The FED is forced to ease rate after the stock market crash."
        },
        {
            value: "Interest Rate",
            time: "May-1989",
            identifier: "H",
            timeformat: "%b-%Y",
            tooltext:
                "Liquidity problem forced the Fed to increase rate to nearly 10%."
        },
        {
            value: "Interest Rate",
            time: "Sept-1992",
            identifier: "L",
            timeformat: "%b-%Y",
            tooltext:
                "To fight the jobless economy growth the Fed had to reduce the interest rate to 3%."
        },
        {
            value: "Interest Rate",
            time: "Jun-2003",
            identifier: "L",
            timeformat: "%b-%Y",
            tooltext:
                "Struggling to revive the economy, the FED cuts its benchmark rate to 1%."
        },
        {
            value: "Interest Rate",
            time: "Sep-2007",
            identifier: "L",
            timeformat: "%b-%Y",
            tooltext: "Fed started reducing the Federal Fund Rate."
        },
        {
            value: "Interest Rate",
            time: "Dec-2008",
            identifier: "L",
            timeformat: "%b-%Y",
            tooltext:
                "Fed reduced the interest rates to sub 0.25% to manage the menace of longest economic downturn since World War 2"
        }
    ]
};

class ChartViewer extends React.Component {
    constructor(props) {
        super(props);
        this.onFetchData = this.onFetchData.bind(this);
        this.state = {
            timeseriesDs: {
                type: "timeseries",
                renderAt: "container",
                width: "65%",
                height: "400",
                dataSource
            }
        };
    }

    componentDidMount() {
        this.onFetchData();
    }

    onFetchData() {
        Promise.all([dataFetch, schemaFetch]).then(res => {
            const data = res[0];
            const schema = res[1];
            const fusionTable = new FusionCharts.DataStore().createDataTable(
                data,
                schema
            );
            const timeseriesDs = Object.assign({}, this.state.timeseriesDs);
            timeseriesDs.dataSource.data = fusionTable;
            this.setState({
                timeseriesDs
            });
        });
    }

    render() {
        return (
            <div>
                {this.state.timeseriesDs.dataSource.data ? (
                    <ReactFC {...this.state.timeseriesDs} />
                ) : (
                    "loading"
                )}
            </div>
        );
    }
}

export default ChartViewer