import React, { Component } from 'react'
import { Chart } from 'react-chartjs-2'
import Papa from 'papaparse'

let deathChart;

let dates = [];

let moDeathsArray = [];
let ilDeathsArray = [];
let akDeathsArray = [];
let ksDeathsArray = [];
let okDeathsArray = [];

let moNewDeathsArray = [];
let ilNewDeathsArray = [];
let akNewDeathsArray = [];
let ksNewDeathsArray = [];
let okNewDeathsArray = [];

function Do(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['state'] === 'Missouri') {
            moDeathsArray.push(data[i]['deaths'])
            dates.push(data[i]['report_date'])
        }
        if (data[i]['state'] === 'Illinois') {
            ilDeathsArray.push(data[i]['deaths'])
        }
        if (data[i]['state'] === - 'Arkansas') {
            akDeathsArray.push(data[i]['deaths'])
        }
        if (data[i]['state'] === 'Kansas') {
            ksDeathsArray.push(data[i]['deaths'])
        }
        if (data[i]['state'] === 'Oklahoma') {
            okDeathsArray.push(data[i]['deaths'])
        }
    }
}

function Da(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['state'] === 'Missouri') {
            moNewDeathsArray.push(data[i]['new_deaths'])
            dates.push(data[i]['report_date'])
        }
        if (data[i]['state'] === 'Illinois') {
            ilNewDeathsArray.push(data[i]['new_deaths'])
        }
        if (data[i]['state'] === - 'Arkansas') {
            akNewDeathsArray.push(data[i]['new_deaths'])
        }
        if (data[i]['state'] === 'Kansas') {
            ksNewDeathsArray.push(data[i]['new_deaths'])
        }
        if (data[i]['state'] === 'Oklahoma') {
            okNewDeathsArray.push(data[i]['new_deaths'])
        }
    }
}

function switchTotal() {
    deathChart.destroy()
    options['title']['text'] = 'Deaths in Different States'
    data['datasets'][0]['data'] = moDeathsArray
    data['datasets'][1]['data'] = ilDeathsArray
    data['datasets'][2]['data'] = akDeathsArray
    data['datasets'][3]['data'] = ksDeathsArray
    data['datasets'][4]['data'] = okDeathsArray
    let newChart = new Chart('deathChart', {
        type: 'line',
        data: data,
        options: options
    })
    deathChart = newChart
}

function switchNew() {
    deathChart.destroy()
    options['title']['text'] = 'New Deaths in Different States'
    data['datasets'][0]['data'] = moNewDeathsArray
    data['datasets'][1]['data'] = ilNewDeathsArray
    data['datasets'][2]['data'] = akNewDeathsArray
    data['datasets'][3]['data'] = ksNewDeathsArray
    data['datasets'][4]['data'] = okNewDeathsArray
    let newChart = new Chart('deathChart', {
        type: 'line',
        data: data,
        options: options
    })
    deathChart = newChart
}

function parseData(url) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            Do(results.data);
            Da(results.data)
            deathChart.update()
        }
    });
}

let data = {
    labels: dates,
    datasets: [
        {
            label: 'Total Deaths in Missouri',
            data: moDeathsArray,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
            ],

            borderWidth: 1,
        },

        {
            label: "Total Deaths in Illinois",
            data: ilDeathsArray,

            backgroundColor: [
                'rgba(54, 162, 235, 0.2)'
            ],

            borderColor: [
                'rgba(54, 162, 235, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "Total Deaths in Arkansas",
            data: akDeathsArray,

            backgroundColor: [
                'rgba(255, 206, 86, 0.2)'
            ],

            borderColor: [
                'rgba(255, 206, 86, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "Total Deaths in Kansas",
            data: ksDeathsArray,

            backgroundColor: [
                'rgba(75, 192, 192, 0.2)'
            ],

            borderColor: [
                'rgba(75, 192, 192, 1)'
            ],

            borderWidth: 1,


        },

        {
            label: "Total Deaths in Oklahoma",
            data: okDeathsArray,

            backgroundColor: [
                'rgba(153, 102, 255, 0.2)'
            ],

            borderColor: [
                'rgba(153, 102, 255, 1)'
            ],

            borderWidth: 1,


        }
    ],
}

let options = {
    elements: {
        point: {
            radius: 1
        }
    },
    legend: {
        labels: {
            fontSize: 15,
        }
    },
    responsiveAnimationDuration: 1050,
    title: {
        display: true,
        text: 'Deaths in Different States',
        fontSize: 30
    },
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    beginAtZero: true,
                    fontSize: 18,
                },
            },
        ],
        xAxes: [{
            ticks: {
                fontSize: 18,
            },
            type: 'time',
            time: {
                unit: 'month',
                unitStepSize: 1,
                displayFormats: {
                    'month': 'MMM YYYY',
                }
            }
        }],
    },
}

class DeathChart extends Component {

    componentDidMount() {
        deathChart = new Chart('deathChart', {
            type: 'line',
            data: data,
            options: options,
        })
        parseData('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/master/data/state/state_full.csv')
    }

    componentWillUnmount() {
        deathChart.destroy()
        dates = [];
        moDeathsArray = [];
        ilDeathsArray = [];
        akDeathsArray = [];
        ksDeathsArray = [];
        okDeathsArray = [];
    }

    render() {
        return (
            <div style={{ position: "relative", height: 650 }}>
                <div className="buttonContainer">
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchTotal()}>Total Deaths</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchNew()}>New Deaths</div>
                    </button>
                </div>
                <div className="chart">
                    <canvas id="deathChart" style={{ width: 600, height: 500, backgroundColor: "white", marginTop: 30, borderRadius: 8 }}></canvas>
                </div>
            </div >
        )
    }
}

export default DeathChart