import React, { Component } from 'react'
import MaterialTable from 'material-table'
import Papa from 'papaparse'

const columns = [
    {
        title: 'County',
        field: 'County',
    },
    {
        title: 'Estimated Cases',
        field: 'Estimated Cases',
        filtering: false,
    },
    {
        title: 'Cases per Capita',
        field: 'Cases per Capita',
        filtering: false,
    },
    {
        title: 'Estimated Deaths',
        field: 'Estimated Deaths',
        filtering: false,
    },
    {
        title: 'Deaths per Capita',
        field: 'Deaths per Capita',
        filtering: false,
    },
]

class DisparitiesCountyCityDataTable extends Component {

    constructor(props) {
        super(props)
        this.state = {
            data: []
        }
        this.updateData = this.updateData.bind(this)
    }

    updateData(result) {
        let y = [];
        const results = result.data
        for (let i = 0; i < results.length - 1; i++) {
            y.push({
                "County": results[i]['county'],
                "Estimated Cases": results[i]['cases'],
                "Cases per Capita": results[i]['case_rate'].toFixed(3),
                "Estimated Deaths": results[i]['deaths'],
                "Deaths per Capita": results[i]['mortality_rate']
            })
        }
        y.reverse()
        this.setState({ data: y })
    }

    componentDidMount() {
        Papa.parse('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/master/data/individual/stl_race_rates.csv', {
            header: true,
            download: true,
            dynamicTyping: true,
            complete: this.updateData
        });
    }

    componentWillUnmount() {
        this.setState({ data: [] })
    }

    render() {
        return (
            <div style={{ width: '100%' }}>
                <MaterialTable
                    title="County City Cases and Deaths"
                    columns={columns}
                    data={this.state.data}
                    options={{
                        search: true,
                        paging: true,
                        header: true,
                        filtering: true,
                        /*headerStyle: {
                            backgroundColor: '#01579b',
                            color: '#FFF'
                        },*/
                    }}
                />
            </div>
        )
    }
}

export default DisparitiesCountyCityDataTable