import React, { Component } from 'react'
import Papa from 'papaparse'
import { Chart } from 'react-chartjs-2'

let usChart;

let usCases = [];
let dates = [];

function getUSCases(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['Country/Region'] == 'US') {
            usCases.push(data[i]['Confirmed'])
            dates.push(data[i]['Date'])
        }
    }
}

function parseData(url, callBack) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            callBack(results.data);
            usChart.update()
        }
    });
}

let data = {
    labels: dates,
    datasets: [
        {
            label: 'Total Cases in US',
            data: usCases,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
            ],

            borderWidth: 1,
        },
    ],
}

let options = {
    legend: {
        labels: {
            fontSize: 15,
        }
    },
    responsiveAnimationDuration: 1050,
    title: {
        display: true,
        text: 'Total US Cases',
        fontSize: 30
    },
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    beginAtZero: true,
                    fontSize: 18,
                },
            },
        ],
        xAxes: [{
            ticks: {
                fontSize: 18,
            },
            type: 'time',
            time: {
                unit: 'month',
                unitStepSize: 1,
                displayFormats: {
                    'month': 'MMM YYYY',
                }
            }
        }],
    },
}

class USTotalChart extends Component {

    componentDidMount() {
        usChart = new Chart("usChart", {
            type: 'line',
            data: data,
            options: options
        })
        parseData('https://raw.githubusercontent.com/datasets/covid-19/main/data/time-series-19-covid-combined.csv', getUSCases)
    }

    componentWillUnmount() {
        usChart.destroy()
        usCases = [];
        dates = [];
    }

    render() {
        return (
            <>
                <canvas id="usChart" style={{ width: 600, height: 400 }}></canvas>
            </>
        )
    }
}

export default USTotalChart


