import React, { Component } from 'react'
import Papa from 'papaparse'
import { Chart } from 'react-chartjs-2'

let testChart;

let perCapitaInfectionCountyCases = [];
let perCapitaInfectionCityLabels = [];
let perCapitaInfectionCityCases = [];

let cityInfectionCases = [];
let countyInfectionCases = [];
let cityLabels = [];

let cityMortalityCases = [];
let countyMortalityCases = [];

let perCapitaMortalityCityCases = [];
let perCapitaMortalityCountyCases = [];

function doStuff(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['county'] === 'St. Louis City') {
            perCapitaInfectionCityCases.push(Math.round(data[i]['case_rate']))
        }
    }
    perCapitaInfectionCountyCases.push(Math.round(data[1]['case_rate']))
    perCapitaInfectionCountyCases.push(Math.round(data[0]['case_rate']))
    perCapitaInfectionCountyCases.push(Math.round(data[3]['case_rate']))
    perCapitaInfectionCountyCases.push(Math.round(data[2]['case_rate']))
    perCapitaInfectionCountyCases.push('N/A')
    perCapitaInfectionCountyCases.push(Math.round(data[4]['case_rate']))
}

function rada(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['county'] === 'St. Louis City') {
            perCapitaMortalityCityCases.push(Math.round(data[i]['mortality_rate']))
        }
    }
    perCapitaMortalityCountyCases.push(Math.round(data[1]['mortality']))
    perCapitaMortalityCountyCases.push(Math.round(data[0]['mortality']))
    perCapitaMortalityCountyCases.push(Math.round(data[3]['mortality']))
    perCapitaMortalityCountyCases.push(Math.round(data[2]['mortality']))
    perCapitaMortalityCountyCases.push('N/A')
    perCapitaMortalityCountyCases.push(Math.round(data[4]['mortality']))
}

function raw(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['county'] === 'St. Louis City') {
            cityLabels.push(data[i]['value'])
            cityInfectionCases.push((data[i]['cases']))
        }
    }
    countyInfectionCases.push((data[1]['cases']))
    countyInfectionCases.push((data[0]['cases']))
    countyInfectionCases.push((data[3]['cases']))
    countyInfectionCases.push((data[2]['cases']))
    countyInfectionCases.push('N/A')
    countyInfectionCases.push((data[4]['cases']))
}

function oof(data) {
    for (let i = 0; i < data.length - 1; i++) {
        if (data[i]['county'] === 'St. Louis City') {
            cityInfectionCases.push((data[i]['deaths']))
        }
    }
    countyMortalityCases.push((data[1]['deaths']))
    countyMortalityCases.push((data[0]['deaths']))
    countyMortalityCases.push((data[3]['deaths']))
    countyMortalityCases.push((data[2]['deaths']))
    countyMortalityCases.push('N/A')
    countyMortalityCases.push((data[4]['deaths']))
}


function parseData(url) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            doStuff(results.data);
            raw(results.data)
            rada(results.data)
            oof(results.data)
            testChart.update()
        }
    });
}

function switchTotalInfection() {
    testChart.destroy()
    data['datasets'][0]['data'] = cityInfectionCases
    data['datasets'][1]['data'] = countyInfectionCases
    options['title']['text'] = 'Cases Based on Race in Saint Louis'
    let newChart = new Chart("raceChart", {
        type: 'bar',
        data: data,
        options: options,
    })
    testChart = newChart
}

function switchTotalMortality() {
    testChart.destroy()
    data['datasets'][0]['data'] = cityMortalityCases
    data['datasets'][1]['data'] = countyMortalityCases
    options['title']['text'] = 'Mortality Based on Race in Saint Louis'
    let newChart = new Chart("raceChart", {
        type: 'bar',
        data: data,
        options: options,
    })
    testChart = newChart
}

function switchPerCapitaInfection() {
    testChart.destroy()
    data['datasets'][0]['data'] = perCapitaInfectionCityCases
    data['datasets'][1]['data'] = perCapitaInfectionCountyCases
    options['title']['text'] = 'Cases Based on Race Per Capita in Saint Louis'
    let newChart = new Chart("raceChart", {
        type: 'bar',
        data: data,
        options: options,
    })
    testChart = newChart
}

function switchPerCapitaMortality() {
    testChart.destroy()
    data['datasets'][0]['data'] = perCapitaMortalityCityCases
    data['datasets'][1]['data'] = perCapitaMortalityCountyCases
    options['title']['text'] = 'Mortality Based on Race Per Capita in Saint Louis'
    let newChart = new Chart("raceChart", {
        type: 'bar',
        data: data,
        options: options,
    })
    testChart = newChart
}

var data = {
    labels: cityLabels,
    datasets: [
        {
            label: 'St. Louis County',
            data: perCapitaInfectionCountyCases,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(153, 102, 255, 1)'
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)'
            ],

            borderWidth: 1,
        },

        {
            label: 'St. Louis City',
            data: perCapitaInfectionCityCases,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(153, 102, 255, 1)'
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(153, 102, 255, 1)'
            ],

            borderWidth: 1,
        },
    ],
}

var options = {
    responsiveAnimationDuration: 1050,
    title: {
        display: true,
        text: 'Cases Based on Race Per Capita',
        fontSize: 30,
    },

    legend: {
        labels: {
            fontSize: 15,
        }
    },
    responsive: true,
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    fontSize: 18,
                    beginAtZero: true,
                },
            },
        ],

        xAxes: [
            {
                ticks: {
                    fontSize: 18,
                }
            },
        ]
    },
}

class RaceChart extends Component {

    componentDidMount() {
        testChart = new Chart("raceChart", {
            type: 'bar',
            data: data,
            options: options
        })
        parseData('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/master/data/individual/stl_race_rates.csv')
    }

    componentWillUnmount() {
        testChart.destroy()
        perCapitaInfectionCountyCases = [];
        perCapitaInfectionCityLabels = [];
        perCapitaInfectionCityCases = [];
        cityInfectionCases = [];
        countyInfectionCases = [];
        cityLabels = [];
        cityMortalityCases = [];
        countyMortalityCases = [];
        perCapitaMortalityCityCases = [];
        perCapitaMortalityCountyCases = [];
    }

    render() {
        return (
            <div>
                <div className="buttonContainer">
                    <button className="pushable" onClick={() => switchTotalInfection()}>
                        <div className="shadow"></div>
                        <div className="front">Total Infection</div>
                    </button>
                    <button className="pushable" onClick={() => switchPerCapitaInfection()}>
                        <div className="shadow"></div>
                        <div className="front">Per Capita Infection</div>
                    </button>
                    <button className="pushable" onClick={() => switchTotalMortality()}>
                        <div className="shadow"></div>
                        <div className="front">Total Mortality</div>
                    </button>
                    <button className="pushable" onClick={() => switchPerCapitaMortality()}>
                        <div className="shadow"></div>
                        <div className="front">Per Capita Mortality</div>
                    </button>
                </div>
                <div className="chart">
                    <canvas id="raceChart" style={{ width: 600, height: 500, backgroundColor: "white", marginTop: 30, borderRadius: 8 }}></canvas>
                </div>
            </div>

        )
    }
}

export default RaceChart
