import React, { Component } from 'react'
import Papa from 'papaparse'
import { Chart } from 'react-chartjs-2'

let disparitiesStateChart;

let inf = []
let death = []
let infR = []
let deathR = []

let labels = ['White', 'Black', 'Native', 'Asian', 'Pacific Islander', 'Two or More']

function all(results) {
    for (let i = 0; i < results.length - 4; i++) {
        inf.push(Math.round(results[i]['cases_est']))
        death.push(Math.round(results[i]['deaths_est']))
        infR.push(results[i]['case_rate'].toFixed(3))
        deathR.push(results[i]['mortality_rate'].toFixed(3))
    }
}

function parseData(url) {
    Papa.parse(url, {
        header: true,
        download: true,
        dynamicTyping: true,
        complete: function (results) {
            all(results.data)
            console.log(inf)
            disparitiesStateChart.update()
        }
    });
}

function switchINF() {
    disparitiesStateChart.destroy()
    options['title']['text'] = 'Total Cases'
    data['datasets'][0]['data'] = inf
    let newChart = new Chart("disparitiesStateChart", {
        type: 'bar',
        data: data,
        options: options
    })
    disparitiesStateChart = newChart
}

function switchINFR() {
    disparitiesStateChart.destroy()
    options['title']['text'] = 'Cases per Capita'
    data['datasets'][0]['data'] = infR
    let newChart = new Chart("disparitiesStateChart", {
        type: 'bar',
        data: data,
        options: options
    })
    disparitiesStateChart = newChart
}

function switchD() {
    disparitiesStateChart.destroy()
    options['title']['text'] = 'Total Deaths'
    data['datasets'][0]['data'] = death
    let newChart = new Chart("disparitiesStateChart", {
        type: 'bar',
        data: data,
        options: options
    })
    disparitiesStateChart = newChart
}

function switchDR() {
    disparitiesStateChart.destroy()
    options['title']['text'] = 'Deaths per Capita'
    data['datasets'][0]['data'] = deathR
    let newChart = new Chart("disparitiesStateChart", {
        type: 'bar',
        data: data,
        options: options
    })
    disparitiesStateChart = newChart
}

var data = {
    labels: labels,
    datasets: [
        {
            label: 'Identities',
            data: inf,

            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(153, 102, 255, 1)'
            ],

            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(153, 102, 255, 1)'
            ],

            borderWidth: 1,
        },
    ]
}

var options = {
    responsiveAnimationDuration: 1050,
    title: {
        display: true,
        text: 'Total Cases',
        fontSize: 30,
    },

    legend: {
        labels: {
            fontSize: 15,
        }
    },
    responsive: true,
    maintainAspectRatio: false,
    scales: {
        yAxes: [
            {
                ticks: {
                    fontSize: 18,
                    beginAtZero: true,
                },
            },
        ],

        xAxes: [
            {
                ticks: {
                    fontSize: 18,
                }
            },
        ]
    },
}

class DisparitiesStateChart extends Component {

    componentDidMount() {
        disparitiesStateChart = new Chart("disparitiesStateChart", {
            type: 'bar',
            data: data,
            options: options
        })
        parseData('https://raw.githubusercontent.com/slu-openGIS/MO_HEALTH_Covid_Tracking/d4d5f6d107614c509a0dc2b9c9ca9164d75f3e5f/data/individual/mo_race_rates.csv')
    }

    componentWillUnmount() {
        disparitiesStateChart.destroy()
        inf = []
        death = []
        infR = []
        deathR = []
    }


    render() {
        return (
            <div>
                <div className="buttonContainer">
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchINF()}>Total Cases</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchINFR()}>Cases per Capita</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchD()}>Total Deaths</div>
                    </button>
                    <button className="pushable">
                        <div className="shadow"></div>
                        <div className="front" onClick={() => switchDR()}>Deaths per Capita</div>
                    </button>
                </div>
                <div className="chart">
                    <canvas id="disparitiesStateChart" style={{ width: 600, height: 500, backgroundColor: "white", marginTop: 30, borderRadius: 8 }}></canvas>
                </div>
            </div>
        )
    }
}

export default DisparitiesStateChart